body {
  display: flex;
  flex-direction: column;
  font-family: var(--ff-prim);
  font-size: var(--fz-base);
  line-height: 1.3;
  color: var(--clr-text);
  background-color: var(--clr-white);
}

.main {
  --pt: clamp(1.88rem, calc(1.67rem + 1.02vw), 2.50rem);
  --pb: clamp(1.88rem, calc(0.65rem + 6.15vw), 5.63rem);
  flex-grow: 1;
  padding: 100px 0 var(--pb);
}

@media (max-width: 1023px) {
  .main {
    padding-top: 80px;
  }
}

a {
  text-decoration: none;
}
