.contacts-content {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
  .contacts-block {
    grid-area: block;
    grid-column: 1 / span 7;
    grid-row: 1 / span 1;
    max-inline-size: 633px;
  }
  .contacts-form-block {
    grid-area: form;
    grid-column: 8 / span 5;
    grid-row: 1 / span 1;
  }
}

@media (max-width: 768px) {
  .contacts-content {
    grid-template-columns: 1fr;
    .contacts-block {
      grid-column: 1 / span 1;
      grid-row: 1 / span 1;
      max-inline-size: unset;
    }
    .contacts-form-block {
      grid-column: 1 / span 1;
      grid-row: 2 / span 1;
    }
  }
}
