.page-quality {
  margin-block-end: 0;
  .quality-top {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    gap: 30px;
    margin-block-end: clamp(1.88rem, calc(1.26rem + 3.07vw), 3.75rem);
    .intro {
      grid-column: 6 / span 4;
      grid-row: 1 / span 1;
    }
    .img {
      overflow: hidden;
      grid-column: 1 / span 5;
      grid-row: 1 / span 2;
      block-size: 100%;
      border-radius: 40px;
      img {
        block-size: 100%;
      }
    }
    .box-hl {
      grid-column: 5 / span 5;
      grid-row: 2 / span 2;
      align-self: start;
    }
    .desc {
      grid-column: 1 / span 4;
      grid-row: 3 / span 1;
      align-self: start;
    }
  }
  .quality-mid {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    gap: 30px;
    margin-block-end: clamp(1.88rem, calc(0.54rem + 6.66vw), 5.94rem);
    .stats {
      grid-column: 1 / span 6;
      grid-row: 1;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 30px;
      .stats-item {
        text-align: center;
        .heading {
          margin-block-end: 10px;
          color: var(--clr-text);
          font-size: 24px;
          font-weight: var(--fw-b);
          line-height: 1.3;
        }
        p {
          color: var(--clr-light-text);
        }
      }
      .stats-item::before {
        content: '';
        display: block;
        inline-size: 60px;
        block-size: 60px;
        margin: 0 auto 10px;
      }
