      }
      .stats-item:nth-child(2)::before {
        background-image: url("data:image/svg+xml,%3Csvg width='61' height='60' viewBox='0 0 61 60' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M34.5773 43.8634H16.965C15.3343 43.8634 14 42.5291 14 40.8983V8.96502C14 7.33426 15.3343 6 16.965 6H42.1084C43.7392 6 45.0735 7.33426 45.0735 8.96502V35.3982' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M40.6414 47.4808C44.4323 47.4808 47.5054 44.4077 47.5054 40.6168C47.5054 36.8259 44.4323 33.7528 40.6414 33.7528C36.8505 33.7528 33.7773 36.8259 33.7773 40.6168C33.7773 44.4077 36.8505 47.4808 40.6414 47.4808Z' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M40.6407 44.3081C42.6794 44.3081 44.3321 42.6554 44.3321 40.6166C44.3321 38.5779 42.6794 36.9252 40.6407 36.9252C38.6019 36.9252 36.9492 38.5779 36.9492 40.6166C36.9492 42.6554 38.6019 44.3081 40.6407 44.3081Z' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M37.3496 46.9621V53.8261L41.1004 50.4905L44.7029 53.8261V46.4877' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M22.124 11.7969H36.9491' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M19.4404 21.2255H39.6322' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M19.4404 16.7782H39.6322' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M19.2627 30.3428H33.4652' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M19.2627 34.9092H30.9894' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M19.2627 25.7919H39.8103' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
      }
