        background-image: url("data:image/svg+xml,%3Csvg width='61' height='60' viewBox='0 0 61 60' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M30.5422 50.7973C37.524 50.7973 43.1839 45.1374 43.1839 38.1556C43.1839 31.1737 37.524 25.5138 30.5422 25.5138C23.5603 25.5138 17.9004 31.1737 17.9004 38.1556C17.9004 45.1374 23.5603 50.7973 30.5422 50.7973Z' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M34.6048 5.9155H26.4613C25.5377 5.9155 24.7891 6.66416 24.7891 7.58769V7.70474C24.7891 8.62827 25.5377 9.37693 26.4613 9.37693H34.6048C35.5283 9.37693 36.277 8.62827 36.277 7.70474V7.58769C36.277 6.66416 35.5283 5.9155 34.6048 5.9155Z' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M26.5452 22.119C19.3214 23.8748 14.0205 30.3796 14.0205 38.1553C14.0205 47.2688 21.4116 54.6766 30.5418 54.6766C39.6719 54.6766 47.063 47.2855 47.063 38.1553C47.063 30.3796 41.7622 23.8748 34.5383 22.119' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M34.5213 22.1195V9.37737H26.5449V22.1195' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M29.9231 46.5494C31.216 46.5494 32.2642 45.5013 32.2642 44.2084C32.2642 42.9154 31.216 41.8673 29.9231 41.8673C28.6302 41.8673 27.582 42.9154 27.582 44.2084C27.582 45.5013 28.6302 46.5494 29.9231 46.5494Z' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M36.779 38.6904C38.4044 38.6904 39.7221 37.3727 39.7221 35.7473C39.7221 34.1219 38.4044 32.8043 36.779 32.8043C35.1536 32.8043 33.8359 34.1219 33.8359 35.7473C33.8359 37.3727 35.1536 38.6904 36.779 38.6904Z' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M24.9397 36.5166C26.1588 36.5166 27.147 35.5283 27.147 34.3093C27.147 33.0902 26.1588 32.102 24.9397 32.102C23.7207 32.102 22.7324 33.0902 22.7324 34.3093C22.7324 35.5283 23.7207 36.5166 24.9397 36.5166Z' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M25.6592 36.5165L28.3849 42.4528' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M27.1475 34.3595L34.1038 34.5434' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M36.278 38.6404L31.8467 42.8544' stroke='%23F4252C' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
      }
    }
    .desc {
      grid-column: 7 / span 3;
      grid-row: 1;
    }
  }
  .quality-btm {
    .one {
      display: grid;
      grid-template-columns: repeat(9, 1fr);
      gap: 30px;
      .text {
        grid-column: 1 / span 5;
        grid-row: 1 / span 1;
      }
      .img {
        overflow: hidden;
        grid-column: 6 / span 4;
        grid-row: 1 / span 1;
        block-size: 153px;
        border-radius: 30px;
        img {
          block-size: 100%;
          border-radius: 30px;
        }
      }
      .stats {
        --min: 100px;
        grid-column: 1 / -1;
        grid-row: 2 / span 1;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(min(100%, var(--min)), 1fr));
        column-gap: 15px;
        .stats-item {
          text-align: center;
          .heading {
            font-size: 24px;
            font-weight: var(--fw-b);
            line-height: 1.3;
          }
          .txt {
            color: var(--clr-light-text);
          }
        }
      }
    }
  }
}

@media (max-width: 640px) {
  .page-quality {
    .quality-top {
      column-gap: unset;
      row-gap: clamp(1.25rem, calc(0.63rem + 3.13vw), 1.88rem);
      .intro {
        grid-column: 1 / -1;
        grid-row: 1 / span 1;
      }
      .img {
        grid-column: 1 / -1;
        grid-row: 2 / span 1;
        block-size: 318px;
        // block-size: 100%;
      }
      .box-hl {
        grid-column: 1 / -1;
        grid-row: 3 / span 1;
      }
      .desc {
        grid-column: 1 / -1;
        grid-row: 4 / span 1;
      }
    }
    .quality-mid {
      column-gap: 0;
      row-gap: 30px;
      .stats {
        grid-column: 1 / span 9;
        grid-row: 1;
      }
      .desc {
        grid-column: 1 / span 9;
        grid-row: 2;
      }
    }
    .quality-btm {
      .one {
        row-gap: 20px;
        column-gap: 0;
        padding: 20px;
        .text {
          grid-column: 1 / -1;
          grid-row: 1 / span 1;
        }
        .img {
          grid-column: 1 / -1;
          grid-row: 2 / span 1;
        }
        .stats {
          grid-row: 3 / span 1;
          row-gap: 20px;
        }
      }
    }
  }
}

@media (max-width: 410px) {
  .page-quality {
    .quality-mid {
      .stats {
        grid-template-columns: 1fr;
        column-gap: 0;
        row-gap: 20px;
      }
    }
  }
}
