.footer-site {
  --r: clamp(3.75rem, calc(3.34rem + 2.05vw), 5.00rem);
  background-color: var(--clr-accent-2);
  border-radius: var(--r) var(--r) 0 0;
}

.footer-inner {
  --t: clamp(1.88rem, calc(1.26rem + 3.07vw), 3.75rem);
  --b: clamp(1.88rem, calc(0.85rem + 5.12vw), 5.00rem);
  padding: var(--t) 0 var(--t);
  display: grid;
  grid-template-areas: 'main nav' 'misc nav';
  // grid-template-columns: minmax(200px, 523px) 1fr;
  grid-template-columns: max-content 1fr;
  column-gap: 30px;
  padding-right: 54px;
  color: var(--clr-white);
  ul {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  a {
    color: var(--clr-white);
    transition: var(--tr);
  }
  a:hover,
  a:focus-visible {
    color: var(--clr-bg-green);
  }
  .main-info {
    grid-area: main;
    margin-bottom: 60px;
    margin-bottom: clamp(0.00rem, calc(-1.23rem + 6.15vw), 3.75rem);
    .img {
      width: 191px;
      height: 84px;
      margin-bottom: 30px;
      margin-bottom: clamp(0.00rem, calc(-0.61rem + 3.07vw), 1.88rem);
    }
    .copyright {
      margin-bottom: 20px;
    }
    address {
      font-style: normal;
    }
  }
  .links-misc {
    grid-area: misc;
  }
  .main-info,
  .links-misc {
    font-size: 14px;
    line-height: 1.3;
  }
  .menu-bottom-wrap {
    grid-area: nav;
    display: flex;
    // justify-content: space-between;
    justify-content: flex-end;
    gap: 80px;
  }
  .products {
    max-width: 210px;
  }
  .heading {
    margin-bottom: 10px;
  }
  .about a,
  .events a,
  .products a {
    font-size: 16px;
    line-height: 1.3;
  }
  .about .heading a,
  .events .heading a,
  .products .heading a {
    font-size: 18px;
  }
}

@media (max-width: 1199px) {
  .footer-inner {
    padding-right: 0;
  }
}

@media (max-width: 1023px) {
  .footer-inner {
    grid-template-areas: 'main main main nav nav nav nav nav nav' 'misc misc misc misc misc misc misc misc misc';
    grid-template-columns: repeat(9, 1fr);
    column-gap: 20px;
    .main-info {
      display: grid;
      grid-template-rows: max-content 1fr max-content;
      margin-block-end: 0;
      .copyright {
        align-self: end;
      }
      address {
        max-inline-size: 199px;
      }
    }
    .links-misc {
      padding-block-start: 60px;
      flex-wrap: wrap;
      flex-direction: row;
      gap: 20px;
    }
    .menu-bottom-wrap {
      display: grid;
      grid-template-columns: max-content max-content minmax(100px, 210px);
      justify-content: space-between;
      column-gap: 20px;
    }
  }
}

@media (max-width: 991px) {
  .footer-inner {
    grid-template-areas: 'main' 'nav' 'misc';
    grid-template-columns: 1fr;
    column-gap: unset;
    row-gap: 30px;
    .main-info {
      grid-template-areas: 'img img' 'copy adr';
      grid-template-columns: max-content 1fr;
      column-gap: 30px;
      .img {
        grid-area: img;
      }
      .copyright {
        grid-area: copy;
        margin-block-end: unset;
      }
      address {
        grid-area: adr;
        max-inline-size: unset;
      }
    }
    .menu-bottom-wrap {
      grid-template-columns: 1fr 1fr 1fr;
    }
    .links-misc {
      flex-wrap: wrap;
      flex-direction: row;
      padding-top: 30px;
    }
  }
}

@media (max-width: 679px) {
  .footer-inner {
    .products {
      max-width: unset;
    }
    .heading {
      width: 100%;
      margin-bottom: unset;
    }
  }
}

@media (max-width: 620px) {
  .footer-inner {
    .main-info {
      grid-template-areas: 'img' 'copy' 'adr';
      grid-template-columns: 1fr;
      .img {
        margin-block-end: unset;
      }
      .copyright {
        margin-block-end: 10px;
      }
    }
  }
}

@media (max-width: 499px) {
  .footer-inner {
    padding-block-start: 40px;
    gap: unset;
    .main-info {
      justify-items: center;
      margin-block-end: 52px;
      text-align: center;
      .img {
        margin-block-end: 5px;
      }
    }
    .heading {
      width: unset;
      margin-block-end: 5px;
    }
    .menu-bottom-wrap {
      grid-template-columns: 1fr;
      justify-items: center;
      gap: 40px;
      ul {
        align-items: center;
        text-align: center;
      }
    }
    .links-misc {
      padding-top: 40px;
      flex-wrap: nowrap;
      flex-direction: column;
      align-items: center;
      gap: 15px;
    }
  }
}
