.products-intro-block {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: max-content max-content 1fr;
  gap: clamp(0.94rem, calc(0.63rem + 1.54vw), 1.88rem);
  margin-block-end: clamp(1.88rem, calc(0.44rem + 7.17vw), 6.25rem);
  .desc {
    grid-column: 1 / span 5;
    grid-row: 1 / span 1;
    h1 {
      margin-block-end: 20px;
    }
    .subtitle {}
  }
  .box-hl {
    grid-column: 1 / span 5;
    grid-row: 2 / span 2;
    align-self: start;
    h3 {
      color: var(--clr-accent-2);
    }
  }
  .drugs {
    justify-self: end;
    grid-column: 6 / span 7;
    grid-row: 1 / span 2;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: clamp(0.94rem, calc(0.63rem + 1.54vw), 1.88rem);
    max-inline-size: 634px;
    .card {
      position: relative;
      block-size: 445px;
      .img {
        overflow: hidden;
        block-size: 100%;
        border-radius: 30px;
        img {
          block-size: 100%;
        }
      }
      .btn-ghost-big {
        position: absolute;
        inset-inline-start: 0;
        inset-block-end: 0;
        transform: translate(30px, -30px);
        svg {
          fill: var(--clr-white);
        }
      }
    }
    .med {
      transform: translateY(-46px);
    }
  }
  .stats-list {
    justify-self: end;
    grid-column: 6 / span 7;
    grid-row: 3 / span 1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: clamp(0.94rem, calc(0.63rem + 1.54vw), 1.88rem);
    text-align: center;
    max-inline-size: 634px;
    .stats-item {
      display: grid;
      .desc {
        .heading {
          margin-block-end: 10px;
          font-size: 24px;
          font-weight: var(--fw-b);
          line-height: 1.3;
        }
      }
      .desc::before {
        content: '';
        display: block;
        inline-size: 60px;
        block-size: 60px;
        // background-color: #d9d9d9;
        margin-inline: auto;
        margin-block-end: 10px;
      }
      // .desc:nth-child(1):before {
      // }
      // .desc:nth-child(1):before {
      // }
      // .desc:nth-child(1):before {
      // }
    }
