    }
  }
}

.subheading-block {
  display: flex;
  align-items: center;
  column-gap: 100px;
  margin-block-end: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
  h2 {
    flex: 0 0 auto;
  }
}

.search-block {
  margin-block-end: clamp(1.88rem, calc(1.57rem + 1.54vw), 2.81rem);
}

.products-more {
  margin-block-end: clamp(1.88rem, calc(0.65rem + 6.15vw), 5.63rem);
}

.products-btm {
  display: grid;
  grid-template-areas: 'ione bone' 'btwo itwo';
  grid-template-columns: 7fr 5fr;
  gap: clamp(0.63rem, calc(0.22rem + 2.05vw), 1.88rem);
  .box-hl {
    padding: clamp(1.25rem, calc(0.64rem + 3.07vw), 3.13rem);
  }
  .img {
    overflow: hidden;
    block-size: 100%;
    border-radius: 40px;
    img {
      block-size: 100%;
    }
  }
  .box-hl.one {
    grid-area: bone;
  }
  .box-hl.two {
    grid-area: btwo;
  }
  .img.one {
    grid-area: ione;
  }
  .img.two {
    grid-area: itwo;
  }
}

@media (max-width: 991px) {
  .products-intro-block {
    column-gap: 0;
    .desc {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
    }
    .box-hl {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
    .drugs {
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
      max-inline-size: unset;
      inline-size: 100%;
      .card {
        block-size: 300px;
      }
      .med {
        transform: unset;
      }
    }
    .stats-list {
      grid-column: 1 / -1;
      grid-row: 4 / span 1;
      justify-self: unset;
      max-inline-size: unset;
    }
  }
  .subheading-block {
    display: block;
    h2 {
      margin-block-end: 20px;
    }
  }
}

@media (max-width: 767px) {
  .products-btm {
    grid-template-areas: 'bone' 'ione' 'btwo' 'itwo';
    grid-template-columns: 1fr;
  }
}

@media (max-width: 640px) {
  .products-intro-block {
    .drugs {
      grid-template-columns: 1fr;
      column-gap: 0;
      row-gap: clamp(0.94rem, calc(0.63rem + 1.54vw), 1.88rem);
    }
  }
}

@media (max-width: 440px) {
  .products-intro-block {
    .stats-list {
      grid-template-columns: 1fr;
      column-gap: 0;
      row-gap: clamp(0.94rem, calc(0.63rem + 1.54vw), 1.88rem);
    }
  }
}
