.page-events {
  h1 {
    margin-block-end: clamp(1.88rem, calc(1.67rem + 1.02vw), 2.50rem);
  }
}

.slider-events-search-wrap {
  .slider-events-search {
    position: relative;
    margin-block-end: 60px;
    .search-slide {
      display: grid;
      grid-template-columns: 442px 80px 1fr 110px;
      grid-template-rows: 30px 322px 30px;
      .box-hl {
        position: relative;
        z-index: 1;
        grid-column: 1 / span 2;
        grid-row: 2 / span 2;
        padding: clamp(1.25rem, calc(0.64rem + 3.07vw), 3.13rem);
      }
      .img {
        overflow: hidden;
        position: relative;
        z-index: 0;
        grid-column: 2 / span 2;
        grid-row: 1 / span 2;
        block-size: 352px;
        border-radius: 30px;
        img {
          block-size: 100%;
          border-radius: 30px;
        }
      }
      .img::before {
        content: '';
        position: absolute;
        inset: 50% 0 0 0;
        border-radius: 30px;
        opacity: 0.6;
        background: linear-gradient(180deg, rgba(2, 13, 17, 0.00) 0%, rgba(3, 15, 19, 0.70) 70.01%);
      }
      .info {
        position: relative;
        z-index: 1;
        grid-column: 3 / span 1;
        grid-row: 2 / span 1;
        align-self: end;
        margin: 0 10px 35px 30px;
        color: var(--clr-white);
        .time {
          font-weight: var(--fw-b);
          margin-block-end: 10px;
        }
      }
    }
    .splide__arrows {
      position: absolute;
      z-index: 10;
      inset-block-end: 0px;
      inset-inline-end: 0;
      display: flex;
      transform: translateY(-30px);
      gap: 15px;
    }
    .splide__arrow {
      align-items: unset;
      background: transparent;
      cursor: pointer;
      display: unset;
      height: unset;
      justify-content: unset;
      opacity: 1;
      position: static;
      top: unset;
      transform: unset;
      width: unset;
      z-index: 1;
      transition: opacity var(--tr);
    }
    .splide__arrow:hover,
    .splide__arrow:focus-visible {
      opacity: .8;
    }
    .splide__arrow:active {
      filter: brightness(0.6);
    }
    .splide__arrow--next {
      transform: rotate(180deg);
    }
  }
}

@media (max-width: 991px) {
  .slider-events-search-wrap {
    .slider-events-search {
      .search-slide {
        grid-template-columns: 100px 1fr 50px 110px;
        grid-template-rows: 100px 233px 1fr;
        .box-hl {
          grid-column: 1 / span 2;
          grid-row: 2 / span 2;
        }
        .img {
          grid-column: 2 / span 2;
          grid-row: 1 / span 2;
          block-size: 332px;
        }
        .img::before {
          inset: 0 0 50% 0;
          background: linear-gradient(0deg, rgba(2, 13, 17, 0.00) 0%, rgba(3, 15, 19, 0.70) 70.01%);
        }
        .info {
          grid-column: 2 / span 1;
          grid-row: 1 / span 1;
          align-self: start;
          margin: 30px 30px 0 30px;
        }
      }
      .splide__arrows {
        inset-block-end: 0px;
        inset-inline-end: 0;
        display: flex;
        transform: translateY(0px);
      }
    }
  }
}

@media (max-width: 767px) {
  .slider-events-search-wrap {
    margin-block-end: 80px;
    .slider-events-search {
      .search-slide {
        grid-template-columns: 30px 1fr 30px;
        grid-template-rows: 120px 233px 1fr;
        .box-hl {
          grid-column: 1 / span 2;
          grid-row: 2 / span 2;
        }
        .img {
          grid-column: 2 / span 2;
          grid-row: 1 / span 2;
          block-size: 332px;
        }
        .info {
          grid-column: 2 / span 1;
          grid-row: 1 / span 1;
          align-self: start;
          margin: 30px 30px 0 30px;
        }
      }
      .splide__arrows {
        inset-block-end: 0px;
        inset-inline-end: 50%;
        transform: translate(50px, 50px);
      }
    }
  }
}

@media (max-width: 400px) {
  .slider-events-search-wrap {
    .slider-events-search {
      .search-slide {
        grid-template-columns: 1fr;
        grid-template-rows: 120px 1fr;
        .box-hl {
          grid-column: 1 / span 1;
          grid-row: 2 / span 2;
        }
        .img {
          grid-column: 1 / span 1;
          grid-row: 1 / span 2;
          block-size: 332px;
        }
        .info {
          grid-column: 1 / span 1;
          grid-row: 1 / span 1;
          align-self: start;
          margin: 30px 30px 0 30px;
        }
      }
    }
  }
}
