.page-drug {
  .content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
  }
  .about {
    grid-column: 6 / -1;
    grid-row: 1 / span 1;
    align-self: start;
    margin-top: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
    margin-bottom: clamp(1.25rem, calc(0.43rem + 4.10vw), 3.75rem);
    padding: clamp(1.25rem, calc(0.64rem + 3.07vw), 3.13rem);
    background-color: var(--clr-bg-green);
    border-radius: clamp(0.94rem, calc(0.63rem + 1.54vw), 1.88rem);
    ul {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }
    li>p+p {
      margin-top: 20px;
    }
    h3 {
      margin-bottom: 20px;
      color: var(--clr-accent-2);
      text-transform: uppercase;
    }
    h4 {
      margin-bottom: 5px;
    }
  }
  .slider {
    grid-column: 1 / span 5;
    grid-row: 1 / span 2;
  }
  .slider-drug-wrap {
    margin-bottom: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
  }
  .desc {
    grid-column: 6 / -1;
    grid-row: 2 / span 1;
    ul {
      display: flex;
      flex-direction: column;
      gap: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
      .list {
        gap: unset;
      }
    }
    h4 {
      margin-bottom: 10px;
    }
  }
  .instuction {
    display: flex;
    align-items: center;
    gap: 20px;
    svg {
      flex-shrink: 0;
      width: 30px;
      height: 30px;
    }
    a {
      color: var(--clr-accent-2);
      transition: color var(--tr);
    }
    a:hover,
    a:focus-visible {
      color: var(--clr-green-2);
    }
  }
}

@media (max-width: 991px) {
  .page-drug {
    .content {
      display: block;
      column-gap: 0;
      .about {
        grid-column: 1 / -1;
        grid-row: 1 / span 1;
      }
      .slider {
        grid-column: 1 / -1;
        grid-row: 2 / span 1;
        max-width: 523px;
        margin-bottom: 30px;
      }
      .desc {
        grid-column: 1 / -1;
        grid-row: 3 / span 1;
        margin-bottom: clamp(1.25rem, calc(0.65rem + 2.98vw), 2.50rem);
      }
    }
  }
}
