.our-responsibility {
  --br: 40px;
  h1 {
    font-size: clamp(1.75rem, calc(1.30rem + 2.25vw), 3.13rem);
  }
  .responsibility-top {
    display: grid;
    grid-template-areas: 'img img img img img img img m m m m m' 'img img img img img img img i i i i i';
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: max-content 1fr;
    gap: 30px;
    .meta {
      grid-area: m;
      justify-self: end;
      display: flex;
      align-items: center;
      gap: 40px;
      block-size: max-content;
      .share {
        display: flex;
        align-items: center;
        color: var(--clr-accent-2);
        transition: color var(--tr);
        svg {
          display: inline-block;
          inline-size: 24px;
          block-size: 24px;
          margin-inline-end: 5px;
        }
      }
      .share:hover,
      .share:focus-visible {
        color: var(--clr-green-2);
      }
    }
    .intro {
      grid-area: i;
      align-self: start;
      padding: 40px 40px 45px;
      background-color: var(--clr-bg);
      border-radius: var(--br);
    }
    .img {
      grid-area: img;
      overflow: hidden;
      block-size: 100%;
      max-block-size: 418px;
      border-radius: var(--br);
      img {
        block-size: 100%;
        border-radius: var(--br);
      }
    }
  }
  h4 {
    margin: 40px 0 10px;
  }
  & p+p {
    margin-block-start: 20px;
  }
  .responsibility-mid {
    margin-block-end: 30px;
  }
  .responsibility-btm {
    // display: flex;
    // align-items: center;
    // gap: 20px;
    padding-block-start: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
    border-block-start: 1px solid var(--clr-border);
    .card-news-block.auto-grid {
      .card-news .card-bottom h4 {
        margin-block-start: 0;
      }
    }
  }
}

@media (max-width: 1023px) {
  .our-responsibility {
    --br: 30px;
    .responsibility-top {
      grid-template-areas: 'm' 'img' 'i';
      grid-template-columns: 1fr;
      grid-template-rows: max-content;
      gap: 20px;
      .meta {
        justify-self: start;
      }
      .intro {
        padding: clamp(1.25rem, calc(0.68rem + 2.84vw), 2.50rem);
      }
      .img {
        aspect-ratio: 1.7799/1;
        max-block-size: unset;
      }
    }
    h4 {
      margin-block-start: clamp(1.88rem, calc(1.59rem + 1.42vw), 2.50rem);
    }
  }
}
