      .page-our-mission {
        .mission-top {
          display: grid;
          grid-template-columns: repeat(9, 1fr);
          grid-template-rows: max-content 1fr;
          column-gap: 30px;
          margin-block-end: clamp(1.88rem, calc(1.26rem + 3.07vw), 3.75rem);
          .subtitle {
            grid-row: 1 / span 1;
            grid-column: 1 / span 5;
            margin-block-end: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
          }
          .img {
            grid-row: 1 / span 2;
            grid-column: 6 / span 4;
            overflow: hidden;
            border-radius: 40px;
            margin-block-start: -132px;
            img {
              border-radius: 40px;
            }
          }
          .box-hl--top {
            grid-row: 2 / span 1;
            grid-column: 1 / span 5;
            align-self: start;
          }
        }
        .mission-mid {
          display: grid;
          grid-template-columns: repeat(9, 1fr);
          grid-template-rows: max-content 1fr;
          gap: 30px;
          margin-block-end: clamp(1.88rem, calc(1.26rem + 3.07vw), 3.75rem);
          .img {
            overflow: hidden;
            grid-row: 1 / span 1;
            grid-column: 1 / span 3;
            max-inline-size: 301px;
            max-block-size: 467px;
            border-radius: 40px;
            img {
              border-radius: 40px;
              block-size: 100%;
            }
          }
          .box-hl--mid {
            grid-row: 1 / span 2;
            grid-column: 4 / span 6;
            padding: clamp(1.25rem, calc(0.64rem + 3.07vw), 3.13rem);
            .values-list {
              display: grid;
              gap: 20px;
              counter-reset: partners;
              .values-item {
                display: flex;
                gap: clamp(0.63rem, calc(0.02rem + 1.52vw), 1.25rem);
                .desc {
                  display: flex;
                  flex-direction: column;
                  row-gap: 5px;
                }
              }
            }
            .values-list .values-item::before {
              counter-increment: partners;
              content: '0'counter(partners);
              font-size: 24px;
              line-height: 1.3;
              font-weight: var(--fw-b);
            }
          }
          .small {
            grid-row: 2 / span 1;
            grid-column: 1 / span 3;
          }
        }
        .mission-btm {
          display: grid;
          grid-template-columns: repeat(9, 1fr);
          column-gap: 30px;
          .box-hl--btm {
            grid-row: 1/ span 1;
            grid-column: 1 / span 5;
          }
          .img {
            overflow: hidden;
            grid-row: 1/ span 1;
            grid-column: 6 / span 4;
            block-size: 346px;
            border-radius: 40px;
            img {
              block-size: 100%;
              border-radius: 40px;
            }
          }
        }
      }

      @media (max-width: 1280px) {
        .page-our-mission {
          .mission-top {
            .img {
              margin-block-start: 0;
            }
          }
        }
      }

      @media (max-width: 1150px) {
        .page-our-mission {
          .mission-top {
            column-gap: unset;
            row-gap: 0px;
            .img {
              transform: unset;
            }
            .subtitle {
              grid-row: 2 / span 1;
              grid-column: 1 / -1;
              // margin-block-end: unset;
            }
            .img {
              grid-row: 3 / span 1;
              grid-column: 1 / -1;
              block-size: 300px;
              margin-block-end: 30px;
            }
            .box-hl--top {
              grid-row: 4 / span 1;
              grid-column: 1 / -1;
            }
          }
          .mission-mid {
            grid-template-columns: repeat(9, 1fr);
            grid-template-rows: max-content 1fr;
            gap: 30px;
            .img {
              grid-row: 1 / span 1;
              grid-column: 1 / span 6;
              max-inline-size: 100%;
              max-block-size: 300px;
            }
            .box-hl--mid {
              grid-row: 2 / span 1;
              grid-column: 1 / -1;
            }
            .small {
              grid-row: 1 / span 1;
              grid-column: 7 / span 3;
              align-self: center;
            }
          }
        }
      }

      @media (max-width: 991px) {
        .page-our-mission {
          .mission-top {
            grid-template-columns: repeat(2, 1fr);
            column-gap: 30px;
            .img {
              grid-column: 2 / -1;
              block-size: 340px;
            }
            .box-hl {
              grid-row: 3 / span 1;
              grid-column: 1 / span 1;
            }
          }
        }
      }

      @media (max-width: 640px) {
        .page-our-mission {
          .mission-top {
            .img {
              grid-column: 1 / -1;
              block-size: 340px;
            }
            .box-hl--top {
              grid-row: 4 / span 1;
              grid-column: 1 / -1;
            }
          }
          .mission-mid {
            row-gap: 20px;
            column-gap: unset;
            .img {
              grid-row: 1 / span 1;
              grid-column: 1 / -1;
            }
            .small {
              grid-row: 2 / span 1;
              grid-column: 1 / -1;
              align-self: center;
            }
            .box-hl--mid {
              grid-row: 3 / span 1;
              grid-column: 1 / -1;
            }
          }
          .mission-btm {
            grid-template-columns: repeat(9, 1fr);
            column-gap: unset;
            row-gap: clamp(1.25rem, calc(0.63rem + 3.13vw), 1.88rem);
            .box-hl--btm {
              grid-row: 2/ span 1;
              grid-column: 1 / -1;
            }
            .img {
              grid-row: 1/ span 1;
              grid-column: 1 / -1;
              block-size: 300px;
            }
          }
        }
      }
