.our-geography {
  overflow: hidden;
  position: relative;
  block-size: clamp(27.56rem, calc(20.03rem + 11.76vw), 29.56rem);
  margin-block-end: clamp(3.75rem, calc(3.14rem + 3.07vw), 5.63rem);
  padding-block-start: 1px;
  .img {
    overflow: hidden;
    position: absolute;
    z-index: -1;
    inset-block-start: 0;
    img {
      inline-size: auto;
      block-size: 473px;
      object-position: clamp(-15.00rem, calc(-71.47rem + 88.24vw), 0.00rem) top;
      object-fit: unset;
    }
  }
  h2 {
    margin-block-start: clamp(2.31rem, calc(-17.92rem + 31.62vw), 7.69rem);
    margin-block-end: 20px;
    text-transform: uppercase;
  }
  .towns {
    display: flex;
    flex-wrap: wrap;
    column-gap: 5px;
    row-gap: 10px;
    max-width: clamp(27.88rem, calc(5.05rem + 35.66vw), 33.94rem);
    span {
      padding: 4px 9px;
      background: hsl(0 100% 100% / 0.6);
      font-size: 12px;
      line-height: 1.3;
      border: 1px solid var(--clr-green-2);
      border-radius: 100vw;
    }
  }
  .btn {
    position: absolute;
    inset-inline-end: 109px;
    inset-block-end: 72px;
  }
}

@media (max-width: 1023px) {
  .our-geography {
    block-size: unset;
    // block-size: 1000px;
    .img {
      img {
        block-size: clamp(17.50rem, calc(12.01rem + 27.45vw), 29.56rem);
        object-position: clamp(-25.00rem, calc(-23.64rem + -2.13vw), -24.06rem) top;
      }
    }
    h2 {
      margin-block-start: 0;
      margin-block-end: clamp(10.00rem, calc(6.30rem + 18.49vw), 18.13rem);
      font-size: clamp(1.75rem, calc(1.41rem + 1.71vw), 2.50rem);
      text-align: center;
    }
    .towns {
      // display: none;
      max-width: unset;
      margin-block-end: 20px;
    }
    .btn {
      // display: none;
      position: static;
      inset-inline-end: unset;
      inset-block-end: unset;
    }
  }
}
