.breadcrumbs .text {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  color: var(--clr-light-text);
  font-size: 14px;
  line-height: 1.3;
  // li:not(:last-child)::after {
  //   content: "/";
  //   margin-left: 5px;
  // }
  a {
    color: var(--clr-accent-2);
    font-size: 14px;
    line-height: 1.3;
    text-decoration: none;
    transition: color var(--tr);
    &:hover,
    &:focus-visible {
      color: var(--clr-green-2);
    }
  }
}

