.hero {
  margin-bottom: clamp(2.50rem, calc(1.84rem + 3.28vw), 4.50rem);
  // padding: clamp(1.88rem, calc(1.28rem + 2.99vw), 3.19rem) 0 clamp(0.00rem, calc(-12.88rem + 20.15vw), 3.44rem);
  padding: clamp(1.88rem, calc(1.28rem + 2.99vw), 3.19rem) 0 0;
  background-image: url('../img/misc/hero-bg.png');
  background-repeat: no-repeat;
  background-position: clamp(20.38rem, calc(5.39rem + 23.44vw), 24.38rem) clamp(1.19rem, calc(1.78rem + -0.73vw), 1.31rem);
  .subheading {
    margin-bottom: 20px;
    margin-left: clamp(0.00rem, calc(-26.00rem + 40.66vw), 6.94rem);
    font-size: clamp(1.13rem, calc(1.04rem + 0.41vw), 1.38rem);
    line-height: 1.3;
  }
  h1 {
    margin-bottom: 34px;
    font-size: 60px;
    font-weight: 700;
    text-align: center;
  }
  .subtitle {
    margin-left: clamp(36.56rem, calc(18.29rem + 28.57vw), 41.44rem);
  }
}

@media (max-width: 1023px) {
  .hero {
    padding-top: clamp(1.25rem, calc(0.37rem + 4.41vw), 3.19rem);
    background-position: clamp(0.00rem, calc(-9.27rem + 46.37vw), 20.38rem) clamp(1.31rem, calc(5.50rem + -6.54vw), 4.19rem);
    background-size: clamp(12.19rem, calc(11.22rem + 4.84vw), 14.31rem) clamp(13.63rem, calc(12.49rem + 5.69vw), 16.13rem);
    .subheading {
      margin-bottom: clamp(0.94rem, calc(4.03rem + -4.84vw), 3.06rem);
      margin-left: 0;
    }
    h1 {
      margin-bottom: clamp(1.88rem, calc(5.06rem + -4.98vw), 4.06rem);
      font-size: clamp(2.50rem, calc(1.93rem + 2.84vw), 3.75rem);
    }
    .subtitle {
      margin-left: 60.68%;
    }
  }
}

@media (max-width: 754px) {
  .hero {
    .subtitle {
      margin-left: 0;
    }
  }
}

@media (max-width: 549px) {
  .hero {
    h1 {
      // margin-left: 26.89%;
      margin-left: 0;
      text-align: left;
    }
  }
}
