.header-site {
  --p-i: 30px;
  position: absolute;
  z-index: 2;
  display: grid;
  grid-template-areas: 'logo menu switch burger';
  grid-template-columns: max-content 1fr max-content max-content;
  align-items: center;
  width: 100%;
  padding-inline: var(--p-i);
  background-color: var(--clr-white);
  .img-logo {
    grid-area: logo;
    a {
      display: block;
      width: max-content;
      height: 100%;
    }
    .logo-lg {
      width: 199px;
      height: 60px;
    }
    .logo-mob {
      display: none;
      width: 135px;
      height: 54px;
    }
  }
  .menu-top-wrap {
    grid-area: menu;
    justify-self: center;
  }
  .switch-lang {
    grid-area: switch;
  }
  .burger {
    grid-area: burger;
  }
}

@media (max-width: 1023px) {
  .header-site {
    --p-i: 15px;
    grid-template-areas: 'logo switch burger' 'menu menu menu';
    grid-template-columns: 1fr max-content max-content;
  }
  .header-site.menu-show {
    --mi: 15px;
    width: calc(100% - (var(--mi) * 2));
    margin-inline: var(--mi);
    padding-bottom: 30px;
    box-shadow: 0px 1px 15px 0px rgba(0, 0, 0, 0.05);
    border-radius: 0px 0px 60px 60px;
  }
}

@media (max-width: 799px) {
  .header-site {
    .img-logo {
      .logo-lg {
        display: none;
      }
      .logo-mob {
        display: block;
      }
    }
  }
  .header-site.menu-show {
    height: 98vh;
    width: 100%;
    margin-inline: 0;
    grid-template-rows: max-content 1fr;
  }
}

@media (max-width: 399px) {
  .header-site {
    grid-template-areas: 'logo burger' 'menu menu' 'switch switch';
    grid-template-columns: 1fr max-content;
    grid-template-rows: max-content 1fr max-content;
    // padding-bottom: 30px;
    .switch-lang {
      display: none;
      justify-self: center;
      margin-top: 20px;
    }
    .switch-lang.show {
      display: flex;
    }
  }
}
