.menu-aside {
  align-self: start;
  display: flex;
  flex-direction: column;
  width: max-content;
  padding: 30px 40px;
  background-color: var(--clr-bg-green);
  border-radius: 30px;
  a {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    padding: 10px 0;
    color: var(--clr-text);
    transition: color var(--tr);
    svg {
      width: 20px;
      height: 20px;
      fill: var(--clr-accent-2);
      transition: fill var(--tr);
    }
  }
  a:hover,
  a:focus-visible {
    color: var(--clr-accent-2);
    svg {
      fill: transparent;
    }
  }
  a.current {
    color: var(--clr-accent-2);
    cursor: auto;
    svg {
      fill: transparent;
    }
  }
  &>li+li {
    border-top: 1px solid var(--clr-white);
  }
}
