.contacts-block {
  // display: none;
  .contacts-list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 30px;
    row-gap: 40px;
    margin-block-end: 40px;
    .contacts-item {
      .title {
        display: flex;
        gap: 9px;
        .icon {
          svg {
            inline-size: 24px;
            block-size: 24px;
          }
        }
        .subtitle {
          color: var(--clr-accent-2);
        }
      }
      .address {
        font-style: normal;
      }
      a {
        color: var(--clr-text);
        transition: color var(--tr);
      }
      a:hover,
      a:focus-visible {
        color: var(--clr-accent-2);
      }
    }
    .contacts-item:nth-child(1) {
      inline-size: 100%;
    }
  }
}

@media (max-width: 768px) {
  .contacts-block {
    .contacts-list {
      column-gap: 20px;
      row-gap: 20px;
      margin-block-end: 30px;
    }
  }
}
