  .misc-block {
    --p: clamp(1.88rem, calc(-0.48rem + 3.68vw), 2.50rem);
    --br: 30px;
    position: relative;
    block-size: clamp(26.75rem, calc(37.17rem + -12.87vw), 28.94rem);
    margin-block-end: clamp(3.75rem, calc(2.93rem + 4.10vw), 6.25rem);
    h3 {
      text-transform: uppercase;
    }
    .stats-1 {
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      display: grid;
      gap: 10px;
      inline-size: 301px;
      block-size: 167px;
      padding: var(--p);
      color: var(--clr-white);
      background-color: var(--clr-pressed);
      border-radius: var(--br);
      li {
        display: flex;
        align-items: center;
        gap: 10px;
      }
      .num {
        font-size: 24px;
        font-weight: var(--fw-b);
        line-height: 1.3;
      }
      .txt {
        font-size: 14px;
        font-weight: var(--fw-b);
        line-height: 1.3;
      }
    }
    .img-1 {
      position: absolute;
      z-index: 0;
      left: 110px;
      top: clamp(3.69rem, calc(-8.31rem + 18.75vw), 6.88rem);
      overflow: hidden;
      inline-size: 301px;
      block-size: clamp(11.88rem, calc(24.68rem + -15.81vw), 14.56rem);
      border-radius: var(--br);
      img {
        block-size: 100%;
        border-radius: var(--br);
      }
    }
    .products {
      position: absolute;
      left: clamp(20.00rem, calc(17.65rem + 3.68vw), 20.63rem);
      top: 40px;
      z-index: 1;
      inline-size: clamp(15.44rem, calc(2.79rem + 19.78vw), 18.81rem);
      block-size: clamp(10.69rem, calc(6.00rem + 7.33vw), 11.94rem);
      display: grid;
      align-content: center;
      gap: 10px;
      padding-inline-start: clamp(1.88rem, calc(-0.47rem + 3.66vw), 2.50rem);
      background-color: var(--clr-bg-green);
      border-radius: var(--br);
      a {
        position: absolute;
        z-index: 1;
        inset: 0;
      }
    }
    .btn {
      position: absolute;
      z-index: 1;
      left: clamp(20.50rem, calc(-6.79rem + 42.65vw), 27.75rem);
      top: clamp(16.25rem, calc(31.74rem + -19.12vw), 19.50rem);
    }
    .calendar {
      position: absolute;
      z-index: 2;
      right: 0;
      top: clamp(2.50rem, calc(21.86rem + -23.90vw), 6.56rem);
      inline-size: clamp(19.25rem, calc(6.60rem + 19.78vw), 22.63rem);
      block-size: 191px;
      display: grid;
      align-content: center;
      gap: 10px;
      padding-inline-start: clamp(1.88rem, calc(-0.47rem + 3.66vw), 2.50rem);
      background-color: var(--clr-bg-green);
      border-radius: var(--br);
      a {
        position: absolute;
        z-index: 1;
        inset: 0;
      }
    }
    .btn-wrap {
      position: absolute;
      z-index: 1;
      right: 0;
      top: clamp(2.50rem, calc(21.86rem + -23.90vw), 6.56rem);
      inline-size: clamp(9.00rem, calc(-2.06rem + 17.28vw), 11.94rem);
      block-size: 285px;
      background-color: var(--clr-bg-green);
      border-radius: var(--br);
      .btn-ghost {
        position: absolute;
        left: 30px;
        bottom: 30px;
        background-color: var(--clr-bg-green);
      }
    }
    .btn-wrap::before {
      content: url('../img/svg/patch.svg');
      position: absolute;
      z-index: 1;
      left: 0;
      bottom: 0;
      inline-size: 48px;
      block-size: 46px;
      transform: translate(-100%, -48px);
    }
    .stats-2 {
      position: absolute;
      right: clamp(10.25rem, calc(-3.16rem + 20.96vw), 13.81rem);
      top: clamp(16.25rem, calc(32.93rem + -20.59vw), 19.75rem);
      display: grid;
      align-content: center;
      gap: 15px;
      inline-size: clamp(12.00rem, calc(0.94rem + 17.28vw), 14.94rem);
      block-size: 167px;
      padding-inline-start: clamp(1.88rem, calc(-0.47rem + 3.66vw), 2.50rem);
      color: var(--clr-white);
      background-color: var(--clr-accent-2);
      border-radius: var(--br);
      li {
        display: flex;
        align-items: center;
        gap: 10px;
      }
      .num {
        font-size: 28px;
        font-weight: var(--fw-b);
        line-height: 1.3;
      }
      .txt {
        font-size: 14px;
        line-height: 1.3;
      }
    }
    .img-2 {
      position: absolute;
      left: clamp(27.31rem, calc(-25.16rem + 81.99vw), 41.25rem);
      top: clamp(2.50rem, calc(32.28rem + -36.76vw), 8.75rem);
      overflow: hidden;
      inline-size: clamp(15.31rem, calc(34.07rem + -23.16vw), 19.25rem);
      block-size: clamp(17.50rem, calc(3.38rem + 22.06vw), 21.25rem);
      border-radius: var(--br);
      img {
        block-size: 100%;
        border-radius: var(--br);
      }
    }
  }

  @media (max-width: 944px) {
    .misc-block {
      block-size: auto;
      margin-block-end: 154px;
      .stats-1 {
        position: relative;
        display: flex;
        justify-content: space-evenly;
        gap: 20px;
        inline-size: unset;
        block-size: unset;
        margin-inline-end: 20%;
        margin-block-end: 20px;
      }
      .img-1 {
        position: relative;
        z-index: 0;
        left: 100%;
        top: unset;
        inline-size: 60%;
        block-size: 286px;
        margin-block-start: -80px;
        transform: translateX(-100%);
      }
      .products {
        position: relative;
        left: unset;
        top: unset;
        display: grid;
        inline-size: unset;
        block-size: unset;
        margin-inline-end: 50%;
        margin-block-start: -206px;
        margin-block-end: 20px;
        padding-inline: 30px;
        padding-block: 30px;
        br {
          display: none;
        }
      }
      .btn {
        position: static;
        margin-block-end: 40px;
      }
      .calendar {
        position: relative;
        right: unset;
        top: unset;
        inline-size: unset;
        block-size: unset;
        margin-inline-end: 20%;
        padding: 30px;
        border-radius: 30px 30px 0 30px;
        br {
          display: none;
        }
      }
      .btn-wrap {
        position: relative;
        z-index: 2;
        right: -80%;
        top: 0;
        transform: translateX(-100%);
        display: grid;
        justify-content: center;
        align-items: center;
        inline-size: 83px;
        block-size: 63px;
        border-radius: 0 0 30px 30px;
        .btn-ghost {
          position: static;
          left: 0;
          bottom: 0;
          background-color: var(--clr-bg-green);
        }
      }
      .btn-wrap::before {
        transform: translate(-96%, -17px);
      }
      .stats-2 {
        // display: none;
        position: relative;
        right: unset;
        top: unset;
        display: flex;
        justify-content: space-evenly;
        gap: 15px;
        inline-size: unset;
        block-size: unset;
        margin-inline-end: 50%;
        margin-block-start: -160px;
        padding: 30px;
        br {
          display: none;
        }
      }
      .img-2 {
        position: relative;
        left: 100%;
        top: unset;
        inline-size: 60%;
        block-size: 286px;
        margin-block-start: -150px;
        transform: translateX(-100%);
      }
    }
  }

  @media (max-width: 600px) {
    .misc-block {
      margin-block-end: 124px;
      .stats-1 {
        flex-wrap: wrap;
        margin-inline-end: 0;
        margin-block-end: 10px;
        li {
          inline-size: 220px;
        }
      }
      .img-1 {
        inline-size: 70%;
        block-size: 141px;
        // margin-inline-start: 26.8%;
        margin-block-start: 0;
      }
      .products {
        // display: inline-grid;
        inline-size: 227px;
        margin-inline-end: unset;
        margin-block-start: -70px;
        margin-block-end: 10px;
        br {
          display: inline;
        }
      }
      .calendar {
        margin-inline-end: unset;
      }
      .btn-wrap {
        right: -100%;
      }
      .img-2 {
        inline-size: 70%;
        block-size: 268px;
        margin-block-start: -110px;
      }
      .stats-2 {
        flex-direction: column;
        justify-content: unset;
        inline-size: 187px;
        margin-inline-end: unset;
        margin-block-start: -200px;
      }
    }
  }

  @media (max-width: 520px) {
    .misc-block {}
  }
