.next-events {
  display: grid;
  grid-template-areas: 'desc desc ev2 ev3' 'ev1 ev1 ev2 ev3' 'ev1 ev1 cal cal';
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 30px;
  margin-block-end: clamp(3.75rem, calc(3.14rem + 3.07vw), 5.63rem);
  .desc {
    h2 {
      margin-block-end: 20px;
      text-transform: uppercase;
    }
    p {
      margin-block-end: 10px;
    }
  }
  .event {
    position: relative;
    color: var(--clr-white);
    .link {
      position: absolute;
      z-index: 1;
      inset: 0;
      display: block;
    }
    .info {
      --mi: 30px;
      position: absolute;
      z-index: 1;
      left: 0;
      bottom: 0;
      display: grid;
      grid-template-areas: 'tb tb' 'btn db';
      grid-template-columns: max-content 1fr;
      row-gap: 30px;
      inline-size: calc(100% - ((var(--mi) * 2)));
      margin-block: 40px;
      margin-inline: 30px;
      word-break: break-word;
      pointer-events: none;
      .title-block {
        grid-area: tb;
        pointer-events: none;
        .category {
          margin-block-end: 10px;
        }
      }
      .date-block {
        grid-area: db;
        display: grid;
        justify-items: end;
        gap: 10px;
        pointer-events: none;
      }
      .btn {
        grid-area: btn;
        align-self: end;
        pointer-events: auto;
      }
      .btn--hidden-text {
        padding: 0;
        background-color: transparent;
        span {
          display: none;
        }
        svg {
          fill: currentColor;
        }
      }
    }
    .img {
      overflow: hidden;
      position: relative;
      block-size: 100%;
      min-block-size: 550px;
      border-radius: 30px;
      img {
        block-size: 100%;
        border-radius: 30px;
      }
    }
    .img::before,
    .img::after {
      content: '';
      position: absolute;
      z-index: 0;
      inset: 30% 0 0 0;
      display: block;
      border-radius: 0px 0px 30px 30px;
      transition: inset-block-end var(--tr);
    }
    .img::before {
      inset-block-end: 0;
      background: linear-gradient(180deg, rgba(2, 13, 17, 0.00) 0%, rgba(3, 15, 19, 0.70) 70.01%);
    }
    .img::after {
      inset-block-end: -1000px;
      background: linear-gradient(180deg, rgba(10, 67, 86, 0.00) 0%, rgba(10, 67, 86, 0.80) 70.01%);
    }
  }
  .event:hover,
  .event:focus-visible {
    .img::before {
      inset-block-end: -1000px;
    }
    .img::after {
      inset-block-end: 0;
    }
  }
  .calendar {
    display: grid;
    grid-template-areas: 'h3 a' 'p p';
    grid-template-columns: 1fr max-content;
    padding: 40px;
    color: var(--clr-white);
    background-color: var(--clr-accent-2);
    border-radius: 30px;
    h3 {
      grid-area: h3;
      text-transform: uppercase;
    }
    a {
      grid-area: a;
      align-self: center;
      display: block;
      inline-size: 30px;
      block-size: 30px;
      color: var(--clr-white);
      svg {
        inline-size: 30px;
        block-size: 30px;
        fill: currentColor;
      }
    }
    p {
      grid-area: p;
      margin-block-start: 10px;
    }
  }
  .desc {
    grid-area: desc;
  }
  .ev1 {
    grid-area: ev1;
  }
  .ev2 {
    grid-area: ev2;
  }
  .ev3 {
    grid-area: ev3;
  }
  .calendar {
    grid-area: cal;
  }
}

@media (max-width: 1120px) {
  .next-events {
    grid-template-areas: 'desc desc desc desc desc desc desc desc desc' 'ev1 ev1 ev1 ev1 ev1 . . . .' 'ev1 ev1 ev1 ev1 ev1 ev2 ev2 ev2 ev2' 'ev1 ev1 ev1 ev1 ev1 ev3 ev3 ev3 ev3' 'cal cal cal cal cal ev3 ev3 ev3 ev3';
    grid-template-columns: repeat(9, 1fr);
    gap: 20px;
    .event {
      min-block-size: unset;
    }
    .ev1 {
      block-size: 498px;
      .img {
        min-block-size: 498px;
      }
    }
    .ev2,
    .ev3 {
      block-size: 294px;
      min-block-size: unset;
      max-block-size: unset;
      align-self: end;
      .img {
        min-block-size: 294px;
      }
    }
    .calendar {
      padding: 30px;
    }
  }
}

@media (max-width: 959px) {
  .next-events {
    grid-template-areas: 'desc desc desc' 'ev1 ev2 ev3' 'cal cal cal';
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    .desc {
      h2 {
        margin-block-end: 10px;
      }
    }
    .event {
      .info {
        grid-template-areas: 'db' 'tb' 'btn';
        grid-template-columns: 1fr;
        row-gap: 20px;
        .date-block {
          justify-items: start;
        }
        .btn {
          justify-self: end;
        }
      }
    }
    .ev1,
    .ev2,
    .ev3 {
      block-size: 414px;
      .img {
        min-block-size: 414px;
      }
    }
    .ev1 {
      .info {
        .btn {
          padding: 0;
          background-color: transparent;
          span {
            display: none;
          }
          svg {
            fill: currentColor;
          }
        }
      }
    }
  }
}

@media (max-width: 840px) {
  .next-events {
    grid-template-areas: 'desc' 'wrap' 'cal';
    grid-template-columns: 1fr;
    .wrapper {
      overflow-x: scroll;
      grid-area: wrap;
      display: flex;
      column-gap: 10px;
      padding-block-end: 10px;
    }
    .wrapper>* {
      flex: 0 0 256px;
    }
    .wrapper::-webkit-scrollbar {
      height: .5rem;
    }
    .wrapper::-webkit-scrollbar-track {
      background-color: var(--clr-bg-green);
      border-radius: 100vw;
    }
    .wrapper::-webkit-scrollbar-thumb {
      background-color: var(--clr-accent-2);
      border-radius: 100vw;
    }
  }
}
