.interaction {
  display: grid;
  grid-template-areas: 'h2 b2' 'b1 b2';
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  h2 {
    grid-area: h2;
  }
  .block {
    display: grid;
    block-size: 462px;
    .img {
      position: relative;
      z-index: 0;
      overflow: hidden;
      grid-column: 1 / span 1;
      grid-row: 1 / span 1;
      block-size: 100%;
      border-radius: 40px;
      img {
        block-size: 100%;
        border-radius: 40px;
      }
    }
    .img::before {
      content: '';
      position: absolute;
      inset: 50% 0 0 0;
      border-radius: 0px 0px 40px 40px;
      opacity: 0.6;
      background: linear-gradient(180deg, rgba(2, 13, 17, 0.00) 0%, rgba(3, 15, 19, 0.70) 70.01%);
    }
    .desc {
      position: relative;
      z-index: 1;
      grid-column: 1 / span 1;
      grid-row: 1 / span 1;
      align-self: end;
      padding: 30px;
      color: var(--clr-white);
      h3 {
        margin-bottom: 10px;
        text-transform: uppercase;
      }
      p {
        margin-bottom: 20px;
      }
    }
  }
  .block--1 {
    grid-area: b1;
  }
  .block--2 {
    grid-area: b2;
  }
}

@media (max-width: 1050px) {
  .interaction {
    grid-template-areas: 'h2' 'b1' 'b2';
    grid-template-columns: minmax(200px, 623px);
  }
}

@media (max-width: 550px) {
  .interaction {
    .block {
      min-block-size: 482px;
      margin-inline: -15px;
    }
    .img {
      aspect-ratio: unset;
    }
  }
}
