.events-popup {
  padding: 0;
  background: transparent;
  border: none;
  .events-content {
    display: grid;
    grid-template-columns: 1fr 60px;
    align-items: start;
    column-gap: 10px;
  }
  .events-inner {
    position: relative;
    z-index: 0;
    grid-column: 1 / span 1;
    max-inline-size: 360px;
    padding: 30px;
    background: white;
    border-radius: 20px;
  }
  .events-title {
    margin-block-end: 15px;
    color: var(--clr-accent-2);
    font-size: 18px;
  }
  .events-list {
    overflow-y: auto;
    overflow-x: hidden;
    max-block-size: 370px;
  }
  .events-list::-webkit-scrollbar {
    width: 7px;
  }
  .events-list::-webkit-scrollbar-track {
    background-color: var(--clr-bg-green);
    border-radius: 100vw;
  }
  .events-list::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent-2);
    border-radius: 100vw;
  }
  .events-list>li+li {
    margin-block-start: 10px;
  }
  li {
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
  }
  li::before {
    content: '';
    display: block;
    min-width: 5px;
    min-height: 5px;
    background-color: red;
    border-radius: 50%;
  }
  .popup-btn-close {
    grid-column: 2 / span 1;
    background: transparent;
    border: none;
    outline: none;
    transition: opacity .3s ease-in-out;
  }
  .popup-btn-close:hover {
    opacity: .75;
  }
}

@media (max-width: 500px) {
  .events-popup {
    max-width: calc(100vw - 10px);
    .events-content {
      grid-template-columns: 1fr;
    }
    .events-inner {
      grid-column: unset;
      padding: 15px;
    }
    .popup-btn-close {
      grid-column: unset;
      position: absolute;
      z-index: 2;
      right: 7px;
      top: 7px;
      width: 40px;
      height: 40px;
    }
  }
}

@media (max-width: 400px) {
  .events-popup {
    .events-title {
      max-inline-size: 120px;
    }
  }
}

.events-popup::backdrop {
  background-color: rgba(0, 0, 0, 0.75);
  backdrop-filter: blur(5px);
}

.content.several-events {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  block-size: 100%;
  padding: 2px 2px 20px 2px;
  color: white;
  background-color: #1FC3A1;
  font-size: 14px;
  font-size: clamp(0.88rem, calc(0.67rem + 0.49vw), 1.06rem);
  font-weight: 700;
  cursor: pointer;
  transition: background-color .3s ease-in-out;
}

.content.several-events:hover {
  background-color: #13a084;
}

.content.several-events::after {
  content: '\2039';
  display: block;
  align-self: center;
  inline-size: min-content;
  block-size: min-content;
  margin-block-start: auto;
  margin: 0;
  padding: 0;
  color: white;
  font-size: 40px;
  line-height: 0;
  transform: rotate(-90deg);
}
