.contacts-form {
  display: grid;
  gap: 10px;
  .menu-filter {
    .menu-item {
      inline-size: 100%;
      .level-top {
        justify-content: space-between;
        inline-size: 100%;
      }
    }
  }
  .btm-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: start;
    gap: 20px;
    margin-block-start: 10px;
    .checkbox-wrap {
      display: flex;
      align-items: start;
      gap: 10px;
      span {
        inline-size: 200px;
        a {
          color: var(--clr-accent-2);
        }
      }
    }
  }
}
