.zapros-block {
  overflow: hidden;
  padding: 18px 30px;
  border: 1px solid;
  border-color: var(--clr-border);
  border-radius: 30px;
  cursor: pointer;
  &.change-clr {
    border-color: var(--clr-accent-2);
  }
  .zapros-list-wrap {
    overflow: hidden;
    max-block-size: 0;
    transition: max-block-size .6s ease-in-out;
  }
  .zapros-list-wrap.show {
    max-block-size: 400px;
  }
  .zapros-list {
    margin-block-start: 20px;
  }
  .zapros-list>* {
    margin-block-start: 10px;
  }
  .zapros-item {
    color: var(--clr-text);
    transition: color var(--tr);
  }
  .zapros-item.hidden {
    display: none;
  }
  .zapros-item:hover {
    color: var(--clr-accent-2);
  }
  .zapros-active {
    display: flex;
    justify-content: space-between;
    align-items: center;
    // margin-block-end: 0;
    margin: -18px -30px;
    padding: 18px 30px;
    font-size: 18px;
    line-height: 24px;
    svg {
      inline-size: 24px;
      block-size: 24px;
      transform: rotate(0deg);
      transition: transform var(--tr);
    }
    .def.hidden {
      display: none;
    }
  }
  .zapros-active {
    svg.rotate {
      inline-size: 24px;
      block-size: 24px;
      transform: rotate(180deg);
    }
  }
  .zapros-active.hl {
    background-color: var(--clr-bg-green);
  }
}
