.in-media-block {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;
  column-gap: 30px;
  margin-block-end: clamp(2.50rem, calc(1.68rem + 4.10vw), 5.00rem);
  .desc {
    grid-row: 1 / span 1;
    grid-column: 1 / span 4;
    h3 {
      margin-block-end: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
    }
  }
  .media {
    overflow: hidden;
    grid-row: 1 / span 1;
    grid-column: 5 / span 8;
    border-radius: 30px;
  }
}

@media (max-width: 991px) {
  .in-media-block {
    column-gap: unset;
    row-gap: 20px;
    .desc {
      grid-row: 1 / span 1;
      grid-column: 1 / span 12;
    }
    .media {
      grid-row: 2 / span 1;
      grid-column: 1 / span 12;
    }
  }
}
