.label {
  display: inline-block;
  padding: 5px 10px;
  border-radius: 100vw;
  color: var(--clr-white);
  font-size: 14px;
  line-height: 1.3;
}

.label.next {
  background-color: var(--clr-accent);
}

.label.current {
  background-color: var(--clr-accent-2);
}

.label.prev {
  background-color: var(--clr-light-text);
}

.label.recipe {
  background-color: var(--clr-accent);
}

.label.bud {
  background-color: var(--clr-accent-2);
}

.label.no-recipe {
  background-color: var(--clr-blue);
}
