.page-events-calendar {
  .table-event-calendar-wrap {
    overflow: hidden;
    border: 1px solid var(--clr-border);
    border-radius: 30px;
    margin-block-end: clamp(1.88rem, calc(-2.26rem + 9.74vw), 5.63rem);
    .table-event-calendar {
      inline-size: 100%;
      table-layout: fixed;
      border-collapse: collapse;
      thead {
        color: var(--clr-white);
        background-color: var(--clr-accent-2);
      }
      th,
      td {
        inline-size: 100%;
        margin: 0;
        padding: 0;
        font-size: 18px;
        font-weight: var(--fw-r);
        line-height: 1.3;
      }
      th {
        padding: clamp(0.63rem, calc(0.07rem + 2.78vw), 1.25rem);
        color: var(--clr-white);
      }
      td {
        border-block-start: 1px solid var(--clr-border);
      }
      td+td,
      th+th {
        border-inline-start: 1px solid var(--clr-border);
      }
      .sell {
        display: grid;
        .date {
          justify-self: center;
          inline-size: 100%;
          padding: 10px 2px;
          color: var(--clr-light-text);
          background-color: var(--clr-bg-green);
          text-align: center;
        }
        .date.bright {
          color: var(--clr-text);
        }
        .date.today {
          background-color: var(--clr-hover);
        }
        .content {
          position: relative;
          overflow: hidden;
          min-block-size: clamp(2.50rem, calc(0.28rem + 11.11vw), 5.00rem);
          position: relative;
          // block-size: 80px;
          a.event {
            display: grid;
            inline-size: 100%;
            block-size: 100%;
            padding: 3px;
            color: var(--clr-white);
            background-color: var(--clr-accent-2);
            line-height: 1;
            .name {
              font-size: 14px;
              font-weight: var(--fw-b);
              line-height: 1;
            }
            .place {
              font-size: 12px;
              line-height: 1;
            }
          }
        }
      }
      tr.hidden {
        display: none;
      }
    }
  }
}

@media (max-width: 680px) {
  .page-events-calendar {
    .table-event-calendar-wrap {
      .table-event-calendar {
        .sell {
          .content {
            a.event {
              position: absolute;
              inset-inline-end: 5px;
              inset-block-end: 5px;
              aspect-ratio: 1 / 1;
              inline-size: clamp(1.25rem, calc(0.69rem + 2.78vw), 1.88rem);
              block-size: auto;
              background-color: var(--clr-accent);
              border-radius: 100vw;
              .name {
                display: none;
              }
              .place {
                display: none;
              }
            }
          }
        }
      }
    }
  }
}
