.btn {
  --pb: clamp(0.94rem, calc(0.88rem + 0.31vw), 1.13rem);
  --pi: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
  --fz: clamp(1.00rem, calc(0.96rem + 0.20vw), 1.13rem);
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 18px 30px;
  padding: var(--pb) var(--pi);
  font-family: var(--ff-prim);
  font-size: 18px;
  font-size: var(--fz);
  font-weight: var(--fw-r);
  line-height: 1.3333;
  border-radius: 10px;
  transition: var(--tr);
  svg {
    fill: currentColor;
    width: 24px;
    height: 24px;
    transform: translateX(0px);
    transition: transform var(--tr);
  }
}

.btn--warm {
  color: var(--clr-white);
  background-color: var(--clr-accent);
}

.btn--cool {
  color: var(--clr-accent-2);
  background-color: var(--clr-white);
  border: 1px solid var(--clr-accent-2);
}

.btn--warm:hover,
.btn--warm:focus-visible {
  background-color: var(--clr-hover);
  svg {
    transform: translateX(10px);
  }
}

.btn--warm:active {
  background-color: var(--clr-pressed);
  svg {
    transform: translateX(10px);
  }
}

.btn--cool:hover,
.btn--cool:focus-visible {
  color: var(--clr-white);
  background-color: var(--clr-green-2);
  border-color: var(--clr-green-2);
  svg {
    transform: translateX(10px);
  }
}

.btn--cool:active {
  color: var(--clr-white);
  background-color: var(--clr-accent-2);
  border-color: var(--clr-accent-2);
  svg {
    transform: translateX(10px);
  }
}

///////////////////////////////////////////
.btn-ghost {
  display: flex;
  align-items: center;
  gap: 5px;
  color: var(--clr-accent-2);
  background-color: transparent;
  font-size: 18px;
  line-height: 1.3333;
  transition: var(--tr);
  svg {
    fill: currentColor;
    width: 20px;
    height: 20px;
    transform: translateX(0px);
    transition: transform var(--tr);
  }
}

.btn-ghost:hover,
.btn-ghost:focus-visible {
  color: var(--clr-green-2);
  svg:not(.pharm) {
    transform: translateX(15px);
  }
}

///////////////////////////////////////////
.btn-ghost-big {
  --c: var(--clr-white);
  display: grid;
  grid-template-columns: 1fr 30px;
  align-items: center;
  width: calc(100% - 50px);
  color: var(--c);
  background-color: transparent;
  font-size: 28px;
  line-height: 1.2;
  transition: color var(--tr);
  // span {
  //   flex-grow: 1;
  // }
  svg {
    // flex-grow: 1;
    fill: var(--clr-accent-2);
    width: 30px;
    height: 30px;
    transition: opacity var(--tr);
    opacity: 1;
  }
}

.btn-ghost-big:hover,
.btn-ghost-big:focus-visible {
  color: var(--clr-accent-2);
  svg {
    opacity: 0;
  }
}

.btn-attach {
  display: flex;
  align-items: center;
  padding: 18px 30px;
  justify-content: center;
  color: var(--clr-accent-2);
  background-color: var(--clr-white);
  font-size: 18px;
  line-height: 24px;
  border: 1px solid var(--clr-accent-2);
  border-radius: 10px;
  transition: var(--tr);
  svg {
    inline-size: 24px;
    block-size: 24px;
    fill: var(--clr-accent-2);
    transition: fill var(--tr);
  }
}

.btn-attach:hover,
.btn-attach:focus-visible {
  color: var(--clr-white);
  background-color: var(--clr-green-2);
  svg {
    fill: var(--clr-white);
  }
}

.btn-attach:active {
  color: var(--clr-white);
  background-color: var(--clr-accent-2);
  svg {
    fill: var(--clr-white);
  }
}

.btn-attach.attached {
  color: var(--clr-white);
  background-color: var(--clr-accent-2);
  svg {
    fill: var(--clr-white);
  }
}

//--------------------------------------//
.btn-vacancy {
  display: flex;
  align-items: center;
  padding: 30px;
  color: var(--clr-text);
  background-color: var(--clr-bg-green);
  border-radius: 30px;
  transition: background-color var(--tr);
  .desc {
    flex-grow: 1;
    h4 {
      margin-block-end: 0 !important;
    }
    p {
      margin-block-start: 7px !important;
    }
  }
  svg {
    flex: 0 0 auto;
    inline-size: 30px;
    block-size: 30px;
    fill: var(--clr-accent-2);
  }
}

.btn-vacancy:hover,
.btn-vacancy:focus-visible {
  background-color: var(--clr-green-2);
}

//------- btn event ---------------------//
.btn-event {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 18px 30px;
  background-color: var(--clr-white);
  border: 1px solid var(--clr-accent-2);
  border-radius: 100vw;
  transition: background-color var(--tr);
  svg {
    inline-size: 24px;
    block-size: 24px;
    fill: var(--clr-accent-2);
  }
  &:hover,
  &:focus-visible {
    background-color: var(--clr-bg-green);
  }
  &:active {
    color: var(--clr-white);
    background-color: var(--clr-accent-2);
    svg {
      fill: var(--clr-white);
    }
  }
}

.btn-event.prev {
  svg {
    transform: rotate(180deg);
  }
}

.btn-now {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 18px 30px;
  color: var(--clr-white);
  background-color: var(--clr-accent-2);
  border: 1px solid var(--clr-accent-2);
  border-radius: 100vw;
  transition: var(--tr);
  &:hover,
  &:focus-visible {
    background-color: var(--clr-green-2);
  }
  &:active {
    background-color: var(--clr-accent-2);
  }
  &:active {
    color: var(--clr-accent-2);
    background-color: var(--clr-white);
  }
}

.btn-event-group {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.btn-event-type {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 18px 30px;
  color: var(--clr-accent-2);
  background-color: var(--clr-white);
  border: 1px solid var(--clr-border);
  border-radius: 100vw;
  transition: var(--tr);
  svg {
    inline-size: 24px;
    block-size: 24px;
    fill: currentColor;
  }
  &:hover,
  &:focus-visible {
    background-color: var(--clr-bg-green);
    border-color: var(--clr-accent-2);
  }
  &:active {
    color: var(--clr-white);
    background-color: var(--clr-accent-2);
  }
}

.btn-event-type.active {
  color: var(--clr-white);
  background-color: var(--clr-accent-2);
  border: 1px solid var(--clr-accent-2);
  cursor: auto;
}

