.page-event {
  .subheading {
    margin-block-end: 20px;
  }
  .content-main {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: auto 300px auto auto auto auto;
    gap: 30px;
    margin-block-end: clamp(1.88rem, calc(0.65rem + 6.15vw), 5.63rem);
    &>* {
      align-self: start;
    }
    .img {
      grid-column: 1 / 9;
      grid-row: 1 / 3;
      align-self: stretch;
      overflow: hidden;
      block-size: 352px;
      // block-size: 100%;
      border-radius: 30px;
      img {
        block-size: 100%;
        border-radius: 30px;
      }
    }
    .desc {
      grid-column: 1 / 9;
      grid-row: 3 / 6;
      h4 {
        margin: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem) 0 10px;
      }
      p+p {
        margin-block-start: 20px;
      }
    }
    .table-wrap {
      grid-column: 1 / span 8;
      grid-row: 6 / span 1;
      margin-block-start: clamp(1.88rem, calc(1.26rem + 3.07vw), 3.75rem);
    }
    .table-wrap-inner {
      overflow: hidden;
      inline-size: 100%;
      border: 1px solid var(--clr-border);
      border-radius: 30px;
      .table-event {
        inline-size: 100%;
        border-collapse: collapse;
        thead {
          background-color: var(--clr-accent-2);
        }
        th,
        td {
          padding: clamp(0.63rem, calc(0.42rem + 1.02vw), 1.25rem);
        }
        th {
          color: var(--clr-white);
        }
        tr:nth-child(2n) {
          background-color: var(--clr-bg-green);
        }
        td {
          border-block-start: 1px solid var(--clr-border);
        }
        td+td,
        th+th {
          border-inline-start: 1px solid var(--clr-border);
        }
      }
    }
    .meta {
      grid-column: 9 / -1;
      grid-row: 1 / 2;
      display: flex;
      align-items: center;
      gap: 20px;
      justify-content: end;
      a {
        display: flex;
        align-items: center;
        gap: 5px;
        color: var(--clr-accent-2);
        transition: color var(--tr);
        svg {
          inline-size: 24px;
          block-size: 24px;
        }
      }
      a:hover,
      a:focus-visible {
        color: var(--clr-green-2);
      }
    }
    .details,
    .contacts {
      h4 {
        margin-block-end: 20px;
      }
    }
    .details {
      grid-column: 9 / -1;
      grid-row: 2 / 4;
    }
    .contacts {
      grid-column: 9 / span 4;
      grid-row: 4 / span 1;
    }
    .box-hl {
      .box-list {
        display: grid;
        row-gap: 20px;
      }
      .item {
        display: grid;
        grid-template-columns: max-content 1fr;
        grid-template-rows: max-content 1fr;
        gap: 5px;
        svg {
          grid-column: 1 / span 1;
          grid-row: 1 / span 2;
          inline-size: 23px;
          block-size: 23px;
        }
        .title {
          grid-column: 2 / span 1;
          grid-row: 1 / span 1;
          color: var(--clr-accent-2);
          font-size: 18px;
          line-height: 1.3;
        }
        .txt {
          grid-column: 2 / span 1;
          grid-row: 2 / span 1;
        }
        a {
          color: var(--clr-text);
          transition: color var(--tr);
        }
        a:hover,
        a:focus-visible {
          color: var(--clr-accent-2);
        }
      }
    }
  }
  .content-aside {
    .report-photo {
      margin-block-end: clamp(1.88rem, calc(1.26rem + 3.07vw), 3.75rem);
      .lightgallery-2 {
        display: grid;
        grid-template-areas: 'f1 f1 f1 f1 f1 f1 f2 f2 f2 f3 f3 f3' 'f1 f1 f1 f1 f1 f1 f4 f4 f4 f5 f5 f5';
        grid-template-columns: repeat(12, 1fr);
        gap: clamp(0.63rem, calc(0.22rem + 2.05vw), 1.88rem);
        .img {
          overflow: hidden;
          block-size: 204px;
          border-radius: 30px;
          img {
            block-size: 100%;
          }
        }
        .img:nth-child(1) {
          grid-area: f1;
          block-size: 438px;
        }
        .img:nth-child(2) {
          grid-area: f2;
        }
        .img:nth-child(3) {
          grid-area: f3;
        }
        .img:nth-child(4) {
          position: relative;
          grid-area: f4;
        }
        .img:nth-child(5) {
          position: relative;
          grid-area: f5;
        }
        .img:nth-child(n+6) {
          display: none;
        }
        .img:nth-child(5)::before {
          content: '';
          position: absolute;
          inset: 0;
          border-radius: 30px;
          background-color: rgba(0, 0, 0, 0.5);
        }
        .show-more {
          position: relative;
          .num {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            color: white;
            font-size: 30px;
          }
        }
      }
    }
    .report-video {
      display: grid;
      grid-template-areas: 'h3 h3' 'vid desc';
      grid-template-columns: 8fr 4fr;
      gap: clamp(0.63rem, calc(0.22rem + 2.05vw), 1.88rem);
      h3 {
        grid-area: h3;
      }
      .video {
        overflow: hidden;
        grid-area: vid;
        border-radius: 30px;
        img {
          border-radius: 30px;
        }
      }
      .desc {
        grid-area: desc;
        align-self: end;
      }
    }
  }
}

@media (max-width: 991px) {
  .page-event {
    .content-main {
      grid-template-rows: unset;
      row-gap: 30px;
      column-gap: unset;
      .img {
        grid-column: 1 / -1;
        grid-row: 4 / span 1;
      }
      .desc {
        grid-column: 1 / -1;
        grid-row: 5 / span 1;
      }
      .meta {
        grid-column: 1 / -1;
        grid-row: 1 / span 1;
        justify-content: start;
      }
      .table-wrap {
        grid-column: 1 / -1;
        grid-row: 6 / span 1;
      }
      .details {
        grid-column: 1 / -1;
        grid-row: 2 / span 1;
      }
      .contacts {
        grid-column: 1 / -1;
        grid-row: 3 / span 1;
      }
      .box-hl {
        .box-list {
          grid-template-columns: repeat(auto-fill, minmax(min(100%, 230px), 1fr));
          column-gap: 20px;
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .page-event {
    .content-aside {
      .report-photo {
        .lightgallery-2 {
          grid-template-areas: 'f1 f2 f3' 'f4 f6 f5';
          grid-template-columns: repeat(3, 1fr);
          .img:nth-child(1) {
            block-size: 204px;
          }
          .img:nth-child(6) {
            grid-area: f6;
            display: inline;
          }
        }
      }
      .report-video {
        grid-template-areas: 'h3' 'vid' 'desc';
        grid-template-columns: 1fr;
      }
    }
  }
}

@media (max-width: 600px) {
  .page-event {
    .content-main {
      .table-wrap {
        overflow-x: scroll;
        padding-block-end: 20px;
      }
      .table-wrap::-webkit-scrollbar {
        height: .5rem;
      }
      .table-wrap::-webkit-scrollbar-track {
        background-color: var(--clr-bg-green);
        border-radius: 100vw;
      }
      .table-wrap::-webkit-scrollbar-thumb {
        background-color: var(--clr-accent-2);
        border-radius: 100vw;
      }
      .table-wrap-inner {
        inline-size: 600px;
      }
    }
  }
}

@media (max-width: 550px) {
  .page-event {
    .content-aside {
      .report-photo {
        .lightgallery-2 {
          grid-template-areas: 'f1 f2' 'f4 f5';
          grid-template-columns: repeat(2, 1fr);
          .img:nth-child(1) {
            block-size: 204px;
          }
          .img:nth-child(3) {
            grid-area: unset;
            display: none;
          }
          .img:nth-child(6) {
            grid-area: unset;
            display: none;
          }
        }
      }
    }
  }
}

@media (max-width: 400px) {
  .page-event {
    .content-aside {
      .report-photo {
        .lightgallery-2 {
          grid-template-areas: 'f1' 'f2' 'f5';
          grid-template-columns: 1fr;
          column-gap: 0;
          .img:nth-child(3) {
            grid-area: unset;
            display: none;
          }
          .img:nth-child(4) {
            grid-area: unset;
            display: none;
          }
        }
      }
    }
  }
}
