.achievements-block {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
  margin-block-end: clamp(2.50rem, calc(1.48rem + 5.12vw), 5.63rem);
  .achievements-list {
    grid-row: 1 / span 1;
    grid-column: 1 / span 5;
    display: grid;
    gap: 30px;
    margin-block-start: 40px;
  }
  .achievements-item {
    display: grid;
    grid-template-columns: 80px 1fr;
    align-items: center;
    column-gap: 20px;
  }
  .achievements-desc {
    .title {
      margin-block-end: 10px;
      font-size: 24px;
      font-weight: var(--fw-b);
      line-height: 1.3;
    }
    .txt {}
  }
  .data {
    grid-row: 2 / span 1;
    grid-column: 1 / span 5;
  }
  .about {
    grid-row: 1 / span 2;
    grid-column: 6 / span 7;
    justify-self: end;
    max-inline-size: 633px;
    padding: 50px;
    background-color: var(--clr-bg-green);
    border-radius: 30px;
    &>p+p {
      margin-block-start: 20px;
    }
  }
}

@media (max-width: 991px) {
  .achievements-block {
    grid-template-columns: 1fr;
    .achievements-list {
      grid-row: 1 / span 1;
      grid-column: 1 / span 1;
      grid-template-columns: 1fr 1fr;
      margin-block-start: unset;
    }
    .achievements-item {
      display: grid;
      grid-template-columns: 80px 1fr;
      align-items: center;
      column-gap: 20px;
    }
    .achievements-desc {
      .title {
        margin-block-end: 10px;
        font-size: 24px;
        font-weight: var(--fw-b);
        line-height: 1.3;
      }
      .txt {}
    }
    .data {
      grid-row: 2 / span 1;
      grid-column: 1 / span 1;
    }
    .about {
      grid-row: 3 / span 1;
      grid-column: 1 / span 1;
      justify-self: unset;
      max-inline-size: unset;
      padding: 40px;
    }
  }
}

@media (max-width: 767px) {
  .achievements-block {
    .achievements-list {
      grid-template-columns: 1fr;
      margin-block-start: unset;
    }
  }
}

@media (max-width: 479px) {
  .achievements-block {
    .achievements-item {
      column-gap: 10px;
    }
    .about {
      padding: 20px;
    }
  }
}
