.menu-event-calendar {
  display: grid;
  grid-template-columns: max-content max-content max-content 1fr max-content max-content;
  column-gap: 10px;
  margin-block-end: 20px;
  .cal-m-y {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media (max-width: 920px) {
  .menu-event-calendar {
    .btn-event {
      inline-size: 60px;
      block-size: 60px;
      padding: unset;
    }
    .btn-event-type {
      inline-size: 60px;
      block-size: 60px;
      padding: unset;
      span {
        display: none;
      }
    }
  }
}

@media (max-width: 680px) {
  .menu-event-calendar {
    grid-template-columns: 62px 1fr 62px 62px;
    column-gap: 10px;
    row-gap: 10px;
    margin-block-end: 10px;
    .btn-event.prev {
      grid-column: 1 / span 1;
      grid-row: 2 / span 1;
    }
    .btn-event.next {
      grid-column: 4 / span 1;
      grid-row: 2 / span 1;
    }
    .btn-now {
      grid-column: 1 / span 2;
      grid-row: 1 / span 1;
      justify-self: start;
    }
    .cal-m-y {
      grid-column: 2 / span 2;
      grid-row: 2 / span 1;
      font-size: 18px;
    }
    .btn-event-type.dots {
      grid-column: 3 / span 1;
      grid-row: 1 / span 1;
    }
    .btn-event-type.list {
      grid-column: 4 / span 1;
      grid-row: 1 / span 1;
    }
  }
}
