.input,
.textarea {
  padding: 18px 30px;
  background-color: var(--clr-white);
  font-size: 18px;
  line-height: 24px;
  border: 1px solid var(--clr-border);
  border-radius: 100vw;
}

.input:focus-visible,
.textarea:focus-visible {
  background-color: var(--clr-bg-green);
  border: 1px solid var(--clr-accent-2);
  caret-color: var(--clr-accent-2);
  outline: none;
}

.input:focus-visible::placeholder,
.textarea:focus-visible::placeholder {
  color: transparent;
}

.input::placeholder,
.textarea::placeholder {
  color: var(--clr-menu-hover);
}

.input.search {
  inline-size: unset;
  padding: 18px 64px 18px 30px;
  background-image: url('../img/svg/icons/search.svg');
  background-repeat: no-repeat;
  background-position: right 30px center;
  background-size: 24px 24px;
  border: 1px solid var(--clr-accent-2);
}

.input {
  inline-size: 100%;
}

.textarea {
  inline-size: 100%;
  min-block-size: 176px;
  border-radius: 30px;
}

.input-file {
  display: none;
}

@media (max-width: 600px) {
  .input,
  .textarea {
    padding: 12px 20px;
  }
}
