.switch-lang {
  position: relative;
  display: flex;
  width: 80px;
  height: 40px;
  border-radius: 100vw;
  .switch-inner {
    width: 100%;
    height: 100%;
    background-color: var(--clr-white);
    border: 1px solid var(--clr-bg-green);
    border-radius: 100vw;
  }
  .switch-item {
    position: absolute;
    top: 0;
    display: grid;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    color: var(--clr-green-2);
    background-color: transparent;
    border-radius: 100vw;
    cursor: pointer;
  }
  .switch-item.active {
    color: var(--clr-white);
    background-color: var(--clr-accent-2);
  }
  .switch-item.ru {
    left: 0;
  }
  .switch-item.en {
    right: 0;
  }
}
