.card-news-block.auto-grid {
  --min-w: 315px;
}

@media (max-width: 689px) {
  .card-news-block {
    overflow-x: scroll;
    display: flex;
    gap: 10px;
    padding-block-end: 20px;
  }
  .card-news-block>* {
    flex: 0 0 auto;
  }
  .card-news-block::-webkit-scrollbar {
    height: .5rem;
  }
  .card-news-block::-webkit-scrollbar-track {
    background-color: var(--clr-bg-green);
    border-radius: 100vw;
  }
  .card-news-block::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent-2);
    border-radius: 100vw;
  }
}
