.safety-block-wrap {
  --r: clamp(3.75rem, calc(3.34rem + 2.05vw), 5.00rem);
  margin-block-end: clamp(-5.00rem, calc(-3.34rem + -2.05vw), -3.75rem);
  background-color: var(--clr-pressed);
  border-radius: var(--r) var(--r) 0 0;
}

.safety-block {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 30px;
  padding: clamp(1.88rem, calc(0.85rem + 5.12vw), 5.00rem) 0 clamp(5.63rem, calc(3.78rem + 9.22vw), 11.25rem);
  color: var(--clr-white);
  &>* {
    align-self: start;
  }
  .title-block {
    grid-column: 1 / span 7;
    grid-row: 1 / span 1;
    display: flex;
    align-items: center;
    gap: 10px;
    margin-block-end: 10px;
    .img {
      flex-shrink: 0;
      img {
        inline-size: 80px;
        block-size: 80px;
      }
    }
  }
  .desc {
    grid-column: 1 / span 7;
    grid-row: 2 / span 1;
    margin-block-end: 20px;
    &>p+p {
      margin-block-start: 20px;
    }
  }
  .email-list {
    justify-self: end;
    grid-column: 8 / span 5;
    grid-row: 1 / span 4;
    display: grid;
    gap: 30px;
    max-inline-size: 412px;
    li {
      display: grid;
      p:first-child {
        margin-block-end: 10px;
      }
    }
    li>p+p {
      margin-block-start: 5px;
    }
    a {
      display: inline-flex;
      align-items: center;
      column-gap: 5px;
      color: var(--clr-white);
      transition: color var(--tr);
      svg {
        inline-size: 24px;
        block-size: 24px;
        fill: currentColor;
      }
    }
    a:hover,
    a:focus-visible {
      color: var(--clr-hover);
    }
  }
  .tels {
    grid-column: 1 / span 7;
    grid-row: 3 / span 1;
    display: grid;
    gap: 13px;
    a {
      display: inline-flex;
      align-items: center;
      gap: 5px;
      color: var(--clr-white);
      transition: color var(--tr);
      svg {
        block-size: 24px;
        inline-size: 24px;
        fill: currentColor;
      }
    }
    a:hover,
    a:focus-visible {
      color: var(--clr-hover);
    }
  }
}

@media (max-width: 1023px) {
  .safety-block {
    .title-block {
      grid-column: 1 / -1;
      grid-row: 1 / span 1;
    }
    .email-list {
      grid-row: 2 / span 3;
    }
  }
}

@media (max-width: 680px) {
  .safety-block {
    gap: unset;
    .desc {
      grid-column: 1 / -1;
      grid-row: 2 / span 1;
    }
    .email-list {
      grid-column: 1 / -1;
      grid-row: 3 / span 1;
      gap: 20px;
      max-inline-size: unset;
      margin-block-end: 20px;
    }
    .tels {
      grid-column: 1 / -1;
      grid-row: 4 / span 1;
    }
  }
}

@media (max-width: 480px) {
  .safety-block {
    .title-block {
      display: grid;
    }
  }
}
