.card-drug {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  border: 1px solid var(--clr-bg-green);
  border-radius: 30px;
  .card-top {
    overflow: hidden;
    flex-shrink: 0;
    display: grid;
    padding: 30px;
    position: relative;
    .img {
      block-size: 200px;
      img {
        height: 100%;
        object-fit: contain;
      }
    }
    .label {
      position: absolute;
      bottom: 30px;
      left: 30px;
    }
  }
  .card-bottom {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 20px 30px 40px;
    background-color: var(--clr-bg-green);
    .name {
      margin-bottom: 15px;
    }
    .desc {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      gap: 5px;
      margin-bottom: 18px;
      li {
        display: flex;
        flex-direction: column;
        gap: 2px;
      }
    }
    .details {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      gap: 10px;
      .btn-ghost {
        svg {
          width: 30px;
          height: 30px;
        }
      }
    }
  }
}

.card-drug.hidden {
  display: none;
}

@media (max-width: 710px) {
  .card-drug {
    min-inline-size: 270px;
    .card-bottom {
      padding: 15px 20px 30px;
    }
  }
}
