.page-our-jobs {
  margin-block-end: 0;
  .jobs-top {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: max-content max-content 1fr;
    gap: 30px;
    margin-block-end: clamp(1.88rem, calc(0.85rem + 5.12vw), 5.00rem);
    .subtitle {
      grid-column: 1 / span 5;
      grid-row: 1 / span 1;
    }
    .img {
      overflow: hidden;
      grid-column: 6 / span 4;
      grid-row: 1 / span 2;
      block-size: 318px;
      // block-size: 100%;
      border-radius: 40px;
      img {
        block-size: 100%;
      }
    }
    .box-hl {
      grid-column: 1 / span 5;
      grid-row: 2 / span 2;
      .btn--warm {
        margin-block-start: 20px;
      }
    }
    .desc {
      grid-column: 6 / span 4;
      grid-row: 3 / span 1;
    }
  }
  .jobs-quotes {
    margin-block-end: clamp(1.88rem, calc(0.85rem + 5.12vw), 5.00rem);
    h2 {
      margin-block-end: clamp(1.88rem, calc(1.67rem + 1.02vw), 2.50rem);
    }
    .person-block {
      display: grid;
      grid-template-columns: repeat(9, 1fr);
      column-gap: 30px;
      .person {
        grid-column: 1 / span 3;
        grid-row: 1 / span 1;
        .img {
          overflow: hidden;
          inline-size: 191px;
          block-size: 214px;
          margin-block-end: 20px;
          border-radius: 40px;
          img {
            block-size: 100%;
          }
        }
        .desc {
          h4 {
            margin-block-end: 10px;
          }
          p {
            color: var(--clr-light-text);
          }
        }
      }
      .blockquote {
        position: relative;
        grid-column: 4 / span 6;
        grid-row: 1 / span 1;
        padding: 100px 40px 40px;
        background-color: var(--clr-bg-green);
        border-radius: 40px;
        .splide__slide>p+p {
          margin-block-start: 20px;
        }
        .splide__arrows {
          position: absolute;
          z-index: 10;
          inset-block-start: 0px;
          inset-inline-end: 0;
          display: flex;
          transform: translateY(-60px);
          gap: 15px;
        }
        .splide__arrow {
          align-items: unset;
          background: transparent;
          cursor: pointer;
          display: unset;
          height: unset;
          justify-content: unset;
          opacity: 1;
          position: static;
          top: unset;
          transform: unset;
          width: unset;
          z-index: 1;
          transition: opacity var(--tr);
        }
        .splide__arrow:hover,
        .splide__arrow:focus-visible {
          opacity: .8;
        }
        .splide__arrow:active {
          filter: brightness(0.6);
        }
        .splide__arrow--next {
          transform: rotate(180deg);
        }
      }
      .blockquote::before {
        content: '';
        position: absolute;
        inset-block-start: 47px;
        inset-inline-start: 47px;
        inline-size: 30px;
        block-size: 30px;
        background-image: url('../img/svg/icons/quote.svg');
      }
    }
    .person-block+.person-block {
      margin-block-start: clamp(1.88rem, calc(1.26rem + 3.07vw), 3.75rem);
    }
  }
  .jobs-gallery {
    position: relative;
    margin-block-end: clamp(1.88rem, calc(0.85rem + 5.12vw), 5.00rem);
    .desc {
      position: absolute;
      margin-inline-end: 40%;
      pointer-events: none;
      margin-block-end: clamp(1.25rem, calc(0.77rem + 2.38vw), 1.88rem);
      h2 {
        margin-block-end: clamp(0.00rem, calc(-0.20rem + 1.02vw), 0.63rem);
      }
    }
    .lightgallery {
      display: grid;
      grid-template-areas: '. . first' 'sec third forth';
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: max-content max-content;
      gap: 30px;
      .img {
        overflow: hidden;
        block-size: 204px;
        border-radius: 30px;
        img {
          block-size: 100%;
        }
      }
      .img:nth-child(1) {
        grid-area: first;
      }
      .img:nth-child(2) {
        grid-area: sec;
      }
      .img:nth-child(3) {
        grid-area: third;
      }
      .img:nth-child(4) {
        position: relative;
        grid-area: forth;
      }
      .img:nth-child(n+5) {
        display: none;
      }
      .img:nth-child(4)::before {
        content: '';
        position: absolute;
        inset: 0;
        border-radius: 30px;
        background-color: rgba(0, 0, 0, 0.5);
      }
      .show-more {
        position: relative;
        .num {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
          color: white;
          font-size: 30px;
        }
      }
    }
  }
  .jobs-btm {
    .vacancies-other-block {
      h2 {
        margin-block-end: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
      }
      &>.btn-vacancy+.btn-vacancy {
        margin-block-start: 10px;
      }
    }
  }
}

@media (max-width: 740px) {
  .page-our-jobs {
    .jobs-gallery {
      .desc {
        position: static;
        margin-inline-end: unset;
      }
      .lightgallery {
        grid-template-areas: 'first sec' 'third forth';
        grid-template-columns: repeat(2, 1fr);
        gap: clamp(0.63rem, calc(-0.33rem + 4.76vw), 1.88rem);
      }
    }
  }
}

@media (max-width: 680px) {
  .page-our-jobs {
    .jobs-quotes {
      .person-block {
        column-gap: 0;
        row-gap: 30px;
        .person {
          grid-column: 1 / -1;
          grid-row: 1 / span 1;
        }
        .blockquote {
          grid-column: 1 / -1;
          grid-row: 2 / span 1;
          padding: 100px 30px 30px;
          .splide__arrows {
            transform: translateY(-70px);
          }
        }
        .blockquote::before {
          inset-block-start: 32px;
          inset-inline-start: 32px;
        }
      }
    }
  }
}

@media (max-width: 640px) {
  .page-our-jobs {
    .jobs-top {
      column-gap: 0;
      row-gap: clamp(1.25rem, calc(0.63rem + 3.13vw), 1.88rem);
      .subtitle {
        grid-column: 1 / -1;
        grid-row: 1 / span 1;
      }
      .img {
        grid-column: 1 / -1;
        grid-row: 2 / span 1;
      }
      .box-hl {
        grid-column: 1 / -1;
        grid-row: 3 / span 1;
      }
      .desc {
        grid-column: 1 / -1;
        grid-row: 4 / span 1;
      }
    }
  }
}

@media (max-width: 480px) {
  .page-our-jobs {
    .jobs-gallery {
      .lightgallery {
        grid-template-areas: 'first' 'sec' 'third' 'forth';
        grid-template-columns: 1fr;
      }
    }
  }
}

.lg-backdrop.in {
  opacity: .75;
}
