.slider-news-section {
  --br: 40px;
  display: grid;
  grid-template-areas: 'h2 h2 h2 h2 h2 h2 h2 h2 h2 h2 h2 h2' 'ns ns ns ns ns ns ns . . . . .' 'ns ns ns ns ns ns ns sl sl sl sl sl' 'ns ns ns ns ns ns ns . . . . .';
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: max-content 40px 409px 80px;
  column-gap: 30px;
  h2 {
    grid-area: h2;
    margin-block-end: 30px;
  }
  .news-single {
    grid-area: ns;
    position: relative;
    block-size: 527px;
    .img {
      overflow: hidden;
      block-size: 100%;
      border-radius: var(--br);
      img {
        block-size: 100%;
        border-radius: var(--br);
      }
    }
    .desc {
      --mi: 30px;
      position: absolute;
      inset-inline-start: 0;
      inset-block-end: 0;
      inline-size: calc(100% - (var(--m) * 2));
      display: grid;
      row-gap: 10px;
      margin-inline: 30px;
      margin-block-end: 30px;
      color: var(--clr-white);
      pointer-events: none;
      p {
        margin-block-end: 10px;
      }
      .btn-ghost {
        pointer-events: auto;
      }
    }
  }
  .slider-news {
    grid-area: sl;
    align-self: end;
    .news-item {
      position: relative;
      block-size: 100%;
      .img {
        overflow: hidden;
        block-size: 100%;
        border-radius: var(--br);
        img {
          block-size: 100%;
          border-radius: var(--br);
        }
      }
      .desc {
        --mi: 30px;
        position: absolute;
        inset-inline-start: 0;
        inset-block-end: 0;
        display: grid;
        gap: 10px;
        inline-size: calc(100% - ((var(--mi) * 2)));
        margin-inline: 30px;
        margin-block-end: 40px;
        color: var(--clr-white);
        pointer-events: none;
      }
    }
  }
  .splide__arrows {
    position: absolute;
    z-index: 10;
    inset-block-end: 0px;
    inset-inline-start: 0;
    display: flex;
    transform: translateY(80px);
    gap: 15px;
  }
  .splide__arrow {
    align-items: unset;
    background: transparent;
    cursor: pointer;
    display: unset;
    height: unset;
    justify-content: unset;
    opacity: 1;
    position: static;
    top: unset;
    transform: unset;
    width: unset;
    z-index: 1;
    transition: opacity var(--tr);
  }
  .splide__arrow:hover,
  .splide__arrow:focus-visible {
    opacity: .8;
  }
  .splide__arrow:active {
    filter: brightness(0.6);
  }
  .splide__arrow--next {
    transform: rotate(180deg);
  }
  .link {
    overflow: hidden;
    position: absolute;
    inset: 0;
    border-radius: 0px 0px var(--br) var(--br);
  }
  .link::before,
  .link::after {
    content: '';
    position: absolute;
    inset: 40.4% 0 0 0;
    border-radius: 0px 0px var(--br) var(--br);
  }
  .link::before {
    inset-block-end: 0;
    background: linear-gradient(180deg, rgba(2, 13, 17, 0.00) 0%, rgba(3, 15, 19, 0.70) 70.01%);
    transition: inset-block-end var(--tr);
  }
  .link::after {
    inset-block-end: -500px;
    background: linear-gradient(180deg, rgba(10, 67, 86, 0.00) 0%, rgba(10, 67, 86, 0.80) 70.01%);
    transition: inset-block-end var(--tr);
  }
  .link:hover,
  .link:focus-visible {
    &::before {
      inset-block-end: -500px;
    }
    &::after {
      inset-block-end: 0;
    }
  }
}

@media (max-width: 767px) {
  .slider-news-section {
    --br: 30px;
    grid-template-areas: 'h2' 'ns' 'sl';
    grid-template-columns: 1fr;
    grid-template-rows: max-content max-content max-content;
    gap: 0;
    margin-block-end: 60px;
    h2 {
      margin-block-end: 20px;
    }
    .news-single {
      block-size: 500px;
      margin-block-end: 20px;
    }
    .splide__arrows {
      inset-block-end: 0px;
      inset-inline-start: 50%;
      display: flex;
      transform: translate(-50%, 60px);
      gap: 15px;
    }
  }
}
