.container-modal {
  overflow: hidden;
  inline-size: min(692px, 90vw);
  padding: 0 70px 0 0;
  background-color: transparent;
  border: none;
  .modal-inner {
    padding: 50px;
    background-color: white;
    border-radius: 60px;
    height: max-content;
  }
  .contacts-form {
    max-block-size: 66vh;
    padding-inline-end: 20px;
    overflow-y: auto;
  }
  .contacts-form::-webkit-scrollbar {
    width: .5rem;
  }
  .contacts-form::-webkit-scrollbar-track {
    background-color: var(--clr-bg-green);
    border-radius: 100vw;
  }
  .contacts-form::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent-2);
    border-radius: 100vw;
  }
  .btn-close {
    position: absolute;
    top: 0;
    right: 0;
    inline-size: 60px;
    block-size: 60px;
    cursor: pointer;
    opacity: 1;
    transition: opacity var(--tr);
  }
  .btn-close:hover,
  .btn-close:focus-visible {
    opacity: .8;
  }
  .confirmation-desc {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .btns {
    display: flex;
    justify-content: end;
    gap: 10px;
    margin-block-start: 20px;
  }
}

.container-modal::backdrop {
  background-color: hsl(215 62% 19% / .6);
}

@media (max-width: 768px) {
  .container-modal {
    padding: 0 0px 0 0;
    .modal-inner {
      padding: 30px 20px;
      border-radius: 40px;
    }
    .btn-close {
      top: 20px;
      right: 20px;
      inline-size: 50px;
      block-size: 50px;
      cursor: pointer;
    }
  }
}

@media (max-width: 600px) {
  .container-modal {
    .contacts-form-block {
      h4 {
        padding-right: 55px;
      }
      .textarea {
        min-block-size: 100px;
      }
    }
  }
}

@media (max-width: 390px) {
  .container-modal {
    inline-size: 100%;
    max-inline-size: 100%;
    margin-inline: 0;
    .contacts-form-block {
      h4 {
        padding-right: 55px;
      }
    }
    .contacts-form {
      padding-inline-end: 10px;
    }
  }
}
