.about-company {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 30px;
  // margin-bottom: 90px;
  .block1 {
    grid-row: 1 / span 1;
    grid-column: 1 / span 5;
    // max-width: 523px;
    .breadcrumbs {
      margin-bottom: 30px;
    }
    h1 {
      margin-bottom: 20px;
    }
    .subtitle {
      margin-bottom: 40px;
    }
    .desc {
      padding: 50px;
      background-color: var(--clr-bg-green);
      border-radius: 30px;
      h3 {
        margin-bottom: 20px;
        text-transform: uppercase;
      }
      ul {
        margin-bottom: 20px;
        padding-left: 30px;
        list-style-type: disc;
      }
      ul>li+li {
        margin-top: 15px;
      }
    }
    .desc>p+p {
      margin-top: 20px;
    }
  }
  .block2 {
    grid-row: 1 / span 2;
    grid-column: 6 / span 7;
    justify-self: end;
    align-self: end;
    max-inline-size: 634px;
    .info {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 30px;
      margin-bottom: 60px;
      img {
        border-radius: 30px;
      }
      .btn-ghost-big {
        position: absolute;
        bottom: 38px;
        left: 30px;
      }
      .guide {
        margin-top: 60px;
      }
      .docs {
        align-self: start;
      }
    }
    .info .card {
      overflow: hidden;
      position: relative;
      border-radius: 30px;
    }
    .info .card::before {
      content: '';
      position: absolute;
      inset: 50% 0 0 0;
      border-radius: 0px 0px 30px 30px;
      opacity: 0.6;
      background: linear-gradient(180deg, rgba(2, 13, 17, 0.00) 0%, rgba(3, 15, 19, 0.70) 70.01%);
    }
    // .stats {
    //   display: grid;
    //   grid-template-columns: repeat(3, 1fr);
    //   gap: 30px;
    //   text-align: center;
    //   li {
    //     display: flex;
    //     flex-direction: column;
    //     gap: 10px;
    //   }
    //   li::before {
    //     content: '';
    //     position: relative;
    //     left: 50%;
    //     transform: translateX(-50%);
    //     width: 60px;
    //     height: 60px;
    //     background: #D9D9D9;
    //   }
    //   .heading {
    //     font-size: 28px;
    //     font-weight: var(--fw-exb);
    //     line-height: 1.3;
    //   }
    // }
  }
}

@media (max-width: 991px) {
  .about-company {
    column-gap: unset;
    row-gap: 30px;
    margin-block-end: clamp(1.88rem, calc(0.98rem + 4.47vw), 3.75rem);
    .block1 {
      grid-row: 1 / span 1;
      grid-column: 1 / span 12;
      .subtitle {
        margin-bottom: clamp(1.25rem, calc(0.95rem + 1.49vw), 1.88rem);
      }
    }
    .block2 {
      grid-row: 2 / span 1;
      grid-column: 1 / span 12;
      justify-self: unset;
      max-inline-size: unset;
      .info {
        margin-bottom: unset;
        .card {
          inline-size: 100%;
          block-size: 312px;
          img {
            block-size: 100%;
          }
        }
        .guide {
          margin-top: unset;
        }
        .docs {
          align-self: start;
        }
      }
    }
  }
}

@media (max-width: 599px) {
  .about-company {
    row-gap: 20px;
    .block1 {
      .desc {
        padding: clamp(1.25rem, calc(0.53rem + 3.58vw), 1.88rem);
      }
    }
    .block2 {
      .info {
        grid-template-columns: 1fr;
        column-gap: unset;
        row-gap: 20px;
      }
    }
  }
}
