@font-face {
  font-family: "Mulish";
  src: url("../fonts/MulishRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Mulish";
  src: url("../fonts/MulishMedium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Mulish";
  src: url("../fonts/MulishBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Mulish";
  src: url("../fonts/MulishExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Arimo";
  src: url("../fonts/ArimoRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Arimo";
  src: url("../fonts/ArimoSemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Arimo";
  src: url("../fonts/ArimoBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

h1 {
  font-family: var(--ff-sec);
  font-size: 50px;
  font-size: clamp(2.19rem, calc(1.88rem + 1.54vw), 3.13rem);
  font-weight: var(--fw-sb);
  line-height: 1.3;
  text-transform: uppercase;
}

h2 {
  font-family: var(--ff-sec);
  font-size: 40px;
  font-size: clamp(1.75rem, calc(1.50rem + 1.23vw), 2.50rem);
  font-weight: var(--fw-r);
  line-height: 1.3;
  text-transform: uppercase;
}

h3 {
  font-family: var(--ff-sec);
  font-size: 30px;
  font-size: clamp(1.38rem, calc(1.21rem + 0.82vw), 1.88rem);
  font-weight: var(--fw-r);
  line-height: 1.3;
}

h4 {
  font-family: var(--ff-prim);
  // font-size: 26px;
  font-size: clamp(1.38rem, calc(1.29rem + 0.41vw), 1.63rem);
  font-weight: var(--fw-m);
  line-height: 1.2;
}

.subtitle {
  font-family: var(--ff-prim);
  font-size: 18px;
  font-size: clamp(1.00rem, calc(0.96rem + 0.20vw), 1.13rem);
  font-weight: var(--fw-r);
  line-height: 1.3;
}

.bold {
  font-weight: var(--fw-b);
}

.small {
  font-family: var(--ff-prim);
  font-size: .875rem;
  font-weight: var(--fw-r);
  line-height: 1.3;
}
