.container {
  --p-i: 30px;
  position: relative;
  inline-size: min(var(--max-container) + (var(--p-i) * 2), 100%);
  margin: 0 auto;
  padding-inline: var(--p-i);
}

@media (max-width: 1023px) {
  .container {
    --p-i: 15px;
  }
}

.container--header {
  position: relative;
  // max-width: 1296px;
  padding-inline: 0;
}

.auto-grid {
  --min-w: 250px;
  --max-w: 1fr;
  --c-g: 30px;
  --r-g: 30px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(min(var(--min-w), 100%), var(--max-w)));
  column-gap: var(--c-g);
  row-gap: var(--r-g);
}

@media (max-width: 710px) {
  .card-drug-block.auto-grid {
    overflow-x: scroll;
    display: flex;
    gap: 5px;
    padding-block-end: 20px;
  }
  .card-drug-block.auto-grid::-webkit-scrollbar {
    height: .5rem;
  }
  .card-drug-block.auto-grid::-webkit-scrollbar-track {
    background-color: var(--clr-bg-green);
    border-radius: 100vw;
  }
  .card-drug-block.auto-grid::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent-2);
    border-radius: 100vw;
  }
}

.columns-2 {
  display: grid;
  grid-template-areas: 'menu bread' 'menu h1' 'menu cont';
  grid-template-columns: 302px 1fr;
  grid-template-rows: max-content 1fr;
  column-gap: 30px;
  margin-bottom: clamp(2.50rem, calc(1.68rem + 4.10vw), 5.00rem);
  .menu-aside {
    grid-area: menu;
  }
  .breadcrumbs {
    grid-area: bread;
  }
  h1 {
    grid-area: h1;
    margin-bottom: 20px;
  }
  .content-main {
    grid-area: cont;
  }
  @media (max-width: 991px) {
    & {
      display: block;
      .menu-aside {
        display: none;
      }
    }
  }
}

.list {
  padding-left: 25px;
  list-style-type: disc;
}

.box-hl {
  --m: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
  padding: var(--m);
  background-color: var(--clr-bg-green);
  border-radius: var(--m);
  .subheading {
    margin-block-end: 10px;
    font-size: 18px;
    line-height: 1.3;
    color: var(--clr-light-text);
  }
  h3 {
    margin-block-end: 10px;
    text-transform: uppercase;
  }
  .subtitle {
    margin-block-end: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
  }
  &>p+p {
    margin-block-start: 20px;
  }
  .btn {
    margin-block-start: 20px;
  }
  .btn-ghost {
    margin-block-start: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
  }
}

.pos-r {
  position: relative;
}

.p-0 {
  padding: 0 !important;
}

.m-0 {
  margin: 0 !important;
}

.mb-10 {
  margin-bottom: 10px;
}

.mb-20 {
  margin-bottom: 20px;
}

.mb-30 {
  margin-bottom: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
}

.mb-40 {
  margin-bottom: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
}

.mb-60 {
  margin-bottom: clamp(1.88rem, calc(1.26rem + 3.07vw), 3.75rem);
}

.mb-90 {
  margin-bottom: clamp(2.50rem, calc(1.48rem + 5.12vw), 5.63rem);
}

.mr-auto {
  margin-inline-end: auto;
}

.clr-accent {
  color: var(--clr-accent);
}

.clr-accent-2 {
  color: var(--clr-accent-2);
}

.bg-white {
  background-color: var(--clr-white) !important;
}

.uc {
  text-transform: uppercase;
}

.tac {
  text-align: center;
}

.fixed {
  overflow-y: hidden;
}

.visually-hidden {
  position: fixed;
  transform: scale(0);
}

.more {
  display: grid;
  justify-content: center;
  margin-top: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
}

.btm-line::before {
  content: '';
  position: absolute;
  z-index: 1;
  top: 45px;
  width: 100%;
  height: 1px;
  background-color: var(--clr-bg-green);
  pointer-events: none;
}

.grow-1 {
  flex-grow: 1;
}
