.menu-filter {
  position: relative;
  z-index: 10;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
  .menu-item.hidden {
    display: none;
  }
  .level-top {
    display: flex;
    align-items: center;
    gap: 5px;
    padding: 18px 30px;
    background-color: var(--clr-white);
    font-size: 18px;
    line-height: 24px;
    border: 1px solid var(--clr-accent-2);
    border-radius: 100vw;
    svg {
      width: 24px;
      height: 24px;
      fill: var(--clr-accent-2);
    }
    .def.hidden {
      display: none;
    }
  }
  li {
    position: relative;
    .level-top.has-menu-sub {
      transition: background-color var(--tr);
    }
    .level-top.has-menu-sub svg {
      transform: rotate(90deg);
      transition: transform var(--tr);
    }
    .level-top.active {
      background-color: var(--clr-bg-green);
    }
  }
  li:hover,
  li:focus-visible {
    .level-top.has-menu-sub {
      background-color: var(--clr-bg-green);
      svg {
        transform: rotate(-90deg);
      }
    }
    .wrap {
      display: block;
    }
    .level-top.inactive {
      background-color: var(--clr-bg);
      cursor: auto;
      svg {
        transform: rotate(90deg);
      }
    }
  }
  li:nth-child(1) {
    z-index: 30;
  }
  li:nth-child(2) {
    z-index: 20;
  }
  li:nth-child(3) {
    z-index: 10;
  }
  .events-group ul {
    position: relative;
    z-index: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
  .level-top.event {
    color: var(--clr-accent-2);
    border-color: var(--clr-border);
  }
  .level-top.event.pressed {
    color: var(--clr-white);
    background-color: var(--clr-accent-2);
    border-color: var(--clr-accent-2);
    svg {
      fill: var(--clr-white);
    }
  }
  .wrap {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
  }
  .menu-sub {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 10px;
    padding: 30px;
    background-color: var(--clr-white);
    border-radius: 30px;
    box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.25);
    button {
      font-size: 18px;
      line-height: 24px;
      background-color: transparent;
      transition: color var(--tr);
    }
    button:hover,
    button:focus-visible {
      color: var(--clr-accent-2);
    }
  }
}
