.page-404 {
  overflow: hidden;
  display: grid;
  align-content: end;
  aspect-ratio: 1.6877 / 1;
  max-inline-size: 854px;
  margin-inline: auto;
  padding: clamp(1.88rem, calc(1.26rem + 3.07vw), 3.75rem) clamp(1.88rem, calc(0.24rem + 8.20vw), 6.88rem);
  color: var(--clr-white);
  background-image: url('../img/misc/404.jpg');
  background-size: cover;
  border-radius: 40px;
  h1 {
    margin-block-end: 5px;
    font-size: clamp(2.25rem, calc(1.68rem + 2.87vw), 4.00rem);
    line-height: 1.3;
  }
  h4 {
    margin-block-end: 20px;
  }
  .btn {
    justify-self: start;
  }
}

@media (max-width: 400px) {
  .page-404 {
    aspect-ratio: 1 / 1;
  }
}
