.card-event {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  border-radius: 30px;
  .card-top {
    overflow: hidden;
    position: relative;
    flex-shrink: 0;
    height: 287px;
    position: relative;
    .img {
      height: 100%;
      img {
        height: 100%;
      }
    }
    a {
      position: absolute;
      inset: 0;
    }
    a::before {
      content: '';
      position: absolute;
      inset: 30% 0 0 0;
      background: linear-gradient(180deg, rgba(2, 13, 17, 0.00) 0%, rgba(3, 15, 19, 0.70) 70.01%);
      opacity: 1;
      transition: opacity .3s ease-in;
    }
    a::after {
      content: '';
      position: absolute;
      inset: 30% 0 0 0;
      background-image: linear-gradient(180deg, rgba(10, 67, 86, 0.00) 0%, rgba(10, 67, 86, 0.80) 70.01%);
      opacity: 0;
      transition: opacity .3s ease-out;
    }
    a:hover::before {
      opacity: 0;
    }
    a:hover::after {
      opacity: 1;
    }
    .label {
      position: absolute;
      left: 30px;
      top: 30px;
      pointer-events: none;
    }
    .meta {
      position: absolute;
      left: 30px;
      bottom: 30px;
      display: flex;
      flex-direction: column;
      gap: 10px;
      color: var(--clr-white);
      pointer-events: none;
    }
  }
  .card-bottom {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 30px;
    background-color: var(--clr-bg-green);
    .small {
      margin-bottom: 10px;
    }
    h4 {
      margin-bottom: 10px;
    }
    .desc {
      margin-bottom: 23px;
    }
    .btn-ghost {
      // flex-grow: 1;
      align-self: flex-end;
      margin-top: auto;
      svg {
        width: 30px;
        height: 30px;
      }
    }
  }
}
