  display: flex;
  transform: translateY(80px);
  gap: 15px;
}

.slider-news-section .splide__arrow {
  align-items: unset;
  background: transparent;
  cursor: pointer;
  display: unset;
  height: unset;
  justify-content: unset;
  opacity: 1;
  position: static;
  top: unset;
  transform: unset;
  width: unset;
  z-index: 1;
  transition: opacity var(--tr);
}

.slider-news-section .splide__arrow:hover,
.slider-news-section .splide__arrow:focus-visible {
  opacity: 0.8;
}

.slider-news-section .splide__arrow:active {
  filter: brightness(0.6);
}

.slider-news-section .splide__arrow--next {
  transform: rotate(180deg);
}

.slider-news-section .link {
  overflow: hidden;
  position: absolute;
  inset: 0;
  border-radius: 0px 0px var(--br) var(--br);
}

.slider-news-section .link::before,
.slider-news-section .link::after {
  content: "";
  position: absolute;
  inset: 40.4% 0 0 0;
  border-radius: 0px 0px var(--br) var(--br);
}

.slider-news-section .link::before {
  inset-block-end: 0;
