  margin-block-end: clamp(1.88rem, 0.65rem + 6.15vw, 5.63rem);
}

.products-btm {
  display: grid;
  grid-template-areas: "ione bone" "btwo itwo";
  grid-template-columns: 7fr 5fr;
  gap: clamp(0.63rem, 0.22rem + 2.05vw, 1.88rem);
}

