  border-radius: 40px;
}

.products-btm .img img {
  block-size: 100%;
}

.products-btm .box-hl.one {
  grid-area: bone;
}

.products-btm .box-hl.two {
  grid-area: btwo;
}

.products-btm .img.one {
  grid-area: ione;
}

.products-btm .img.two {
  grid-area: itwo;
}

@media (max-width: 991px) {
  .products-intro-block {
    column-gap: 0;
