.page-news-single .news-single-top .meta .share:focus-visible {
  color: var(--clr-green-2);
}

.page-news-single .news-single-top .intro {
  grid-area: i;
  align-self: start;
  padding: 40px 40px 45px;
  background-color: var(--clr-bg);
  border-radius: var(--br);
}

.page-news-single .news-single-top .img {
  grid-area: img;
  overflow: hidden;
  block-size: 100%;
  max-block-size: 418px;
  border-radius: var(--br);
}

.page-news-single .news-single-top .img img {
  block-size: 100%;
  border-radius: var(--br);
}

.page-news-single h4 {
  margin: 40px 0 10px;
}

.page-news-single p + p {
  margin-block-start: 20px;
}

