  padding-block-start: 30px;
  border-block-start: 1px solid var(--clr-border);
}

.page-news-single .news-single-btm .socials.share {
  display: flex;
  gap: 10px;
}

.page-news-single .news-single-btm .socials.share a {
  opacity: 1;
  transition: opacity var(--tr);
}

.page-news-single .news-single-btm .socials.share a svg {
  block-size: 30px;
  inline-size: 30px;
}

.page-news-single .news-single-btm .socials.share a:hover,
