.contacts-block .contacts-list .contacts-item .title .icon svg {
  inline-size: 24px;
  block-size: 24px;
}

.contacts-block .contacts-list .contacts-item .title .subtitle {
  color: var(--clr-accent-2);
}

.contacts-block .contacts-list .contacts-item .address {
