.container-modal {
  overflow: hidden;
  inline-size: min(692px, 90vw);
  padding: 0 70px 0 0;
  background-color: transparent;
  border: none;
}

.container-modal .modal-inner {
  padding: 50px;
  background-color: white;
  border-radius: 60px;
  height: max-content;
}

.container-modal .contacts-form {
  max-block-size: 66vh;
  padding-inline-end: 20px;
  overflow-y: auto;
}

.container-modal .contacts-form::-webkit-scrollbar {
  width: 0.5rem;
}

.container-modal .contacts-form::-webkit-scrollbar-track {
  background-color: var(--clr-bg-green);
  border-radius: 100vw;
}

.container-modal .contacts-form::-webkit-scrollbar-thumb {
  background-color: var(--clr-accent-2);
  border-radius: 100vw;
}

.container-modal .btn-close {
  position: absolute;
  top: 0;
  right: 0;
  inline-size: 60px;
  block-size: 60px;
  cursor: pointer;
  opacity: 1;
  transition: opacity var(--tr);
}

.container-modal .btn-close:hover,
.container-modal .btn-close:focus-visible {
  opacity: 0.8;
}

.container-modal .confirmation-desc {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.container-modal .btns {
  display: flex;
  justify-content: end;
  gap: 10px;
  margin-block-start: 20px;
}

.container-modal::backdrop {
  background-color: hsla(215, 62%, 19%, 0.6);
}

@media (max-width: 768px) {
  .container-modal {
    padding: 0 0px 0 0;
  }
  .container-modal .modal-inner {
    padding: 30px 20px;
    border-radius: 40px;
  }
  .container-modal .btn-close {
    top: 20px;
    right: 20px;
    inline-size: 50px;
    block-size: 50px;
    cursor: pointer;
  }
}
@media (max-width: 600px) {
  .container-modal .contacts-form-block h4 {
    padding-right: 55px;
  }
  .container-modal .contacts-form-block .textarea {
    min-block-size: 100px;
  }
}
@media (max-width: 390px) {
  .container-modal {
    inline-size: 100%;
    max-inline-size: 100%;
    margin-inline: 0;
  }
  .container-modal .contacts-form-block h4 {
    padding-right: 55px;
  }
  .container-modal .contacts-form {
    padding-inline-end: 10px;
  }
}
.events-popup {
  padding: 0;
  background: transparent;
  border: none;
}

.events-popup .events-content {
  display: grid;
  grid-template-columns: 1fr 60px;
  align-items: start;
  column-gap: 10px;
}

.events-popup .events-inner {
  position: relative;
  z-index: 0;
  grid-column: 1/span 1;
  max-inline-size: 360px;
  padding: 30px;
  background: white;
  border-radius: 20px;
}

.events-popup .events-title {
  margin-block-end: 15px;
  color: var(--clr-accent-2);
  font-size: 18px;
}

.events-popup .events-list {
  overflow-y: auto;
  overflow-x: hidden;
  max-block-size: 370px;
}

.events-popup .events-list::-webkit-scrollbar {
  width: 7px;
}

.events-popup .events-list::-webkit-scrollbar-track {
  background-color: var(--clr-bg-green);
  border-radius: 100vw;
}

.events-popup .events-list::-webkit-scrollbar-thumb {
  background-color: var(--clr-accent-2);
  border-radius: 100vw;
}

.events-popup .events-list > li + li {
  margin-block-start: 10px;
}

.events-popup li {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
}

.events-popup li::before {
  content: "";
  display: block;
  min-width: 5px;
  min-height: 5px;
  background-color: red;
  border-radius: 50%;
}

.events-popup .popup-btn-close {
  grid-column: 2/span 1;
  background: transparent;
  border: none;
  outline: none;
  transition: opacity 0.3s ease-in-out;
}

.events-popup .popup-btn-close:hover {
  opacity: 0.75;
}

@media (max-width: 500px) {
  .events-popup {
    max-width: calc(100vw - 10px);
  }
  .events-popup .events-content {
    grid-template-columns: 1fr;
  }
  .events-popup .events-inner {
    grid-column: unset;
    padding: 15px;
  }
  .events-popup .popup-btn-close {
    grid-column: unset;
    position: absolute;
    z-index: 2;
    right: 7px;
    top: 7px;
    width: 40px;
    height: 40px;
  }
}
@media (max-width: 400px) {
  .events-popup .events-title {
    max-inline-size: 120px;
  }
}
.events-popup::backdrop {
  background-color: rgba(0, 0, 0, 0.75);
  backdrop-filter: blur(5px);
}

.content.several-events {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  block-size: 100%;
  padding: 2px 2px 20px 2px;
  color: white;
  background-color: #1FC3A1;
  font-size: 14px;
  font-size: clamp(0.88rem, 0.67rem + 0.49vw, 1.06rem);
  font-weight: 700;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.content.several-events:hover {
  background-color: #13a084;
}

.content.several-events::after {
  content: "";
  display: block;
  align-self: center;
  inline-size: min-content;
  block-size: min-content;
  margin-block-start: auto;
  margin: 0;
  padding: 0;
  color: white;
  font-size: 40px;
  line-height: 0;
  transform: rotate(-90deg);
}

.select {
  overflow: hidden;
  padding: clamp(0.75rem, 0.6088rem + 0.7059vw, 1.125rem) clamp(15px, 0.5846rem + 1.7647vw, 30px);
  border: 1px solid var(--clr-border);
  border-radius: 30px;
  transition: border-color var(--tr);
}

.select__options {
  max-block-size: 0;
  transition: max-block-size var(--tr);
}

.select__options.expand {
  max-block-size: 800px;
}

.select__inner {
  display: grid;
  row-gap: 10px;
  padding-block-start: 20px;
}

.select__option {
  cursor: pointer;
  transition: color var(--tr);
}

.select__option:hover {
  color: var(--clr-accent-2);
}

.select__option.hidden {
  display: none;
}

.select__current {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: -18px clamp(-30px, -0.5846rem - 1.7647vw, -15px);
  padding: 18px clamp(15px, 0.5846rem + 1.7647vw, 30px);
  font-size: 18px;
  line-height: 24px;
  cursor: pointer;
}

.select__current.active {
  background-color: var(--clr-bg-green);
}

.select__arrow {
  flex: 0 0 auto;
  inline-size: 24px;
  block-size: 24px;
  fill: var(--clr-accent-2);
  transform: rotate(90deg);
  transition: transform var(--tr);
}

.select__arrow.rotate {
  transform: rotate(-90deg);
}

.select.active {
  border-color: var(--clr-accent-2);
}

.sub-select__current {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-block-end: 10px;
  font-weight: var(--fw-b);
  cursor: pointer;
}

.sub-select__arrow {
  flex: 0 0 auto;
  inline-size: 24px;
  block-size: 24px;
  fill: var(--clr-accent-2);
  transform: rotate(90deg);
  transition: transform var(--tr);
}

.sub-select__arrow.rotate {
  transform: rotate(-90deg);
}

.sub-select__options {
  overflow: hidden;
  display: grid;
  row-gap: 10px;
  max-block-size: 0;
  margin-inline-start: clamp(15px, 0.5846rem + 1.7647vw, 30px);
  transition: max-block-size var(--tr);
}

.sub-select__options.expand {
  max-block-size: 300px;
}