.select {
  overflow: hidden;
  padding: clamp(0.75rem, 0.6088rem + 0.7059vw, 1.125rem) clamp(15px, 0.5846rem + 1.7647vw, 30px);
  border: 1px solid var(--clr-border);
  border-radius: 30px;
  transition: border-color var(--tr);
  &__options {
    max-block-size: 0;
    transition: max-block-size var(--tr);
  }
  &__options.expand {
    max-block-size: 800px;
  }
  &__inner {
    display: grid;
    row-gap: 10px;
    padding-block-start: 20px;
  }
  &__option {
    cursor: pointer;
    transition: color var(--tr);
  }
  &__option:hover {
    color: var(--clr-accent-2);
  }
  &__option.hidden {
    display: none;
  }
  &__current {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: -18px clamp(-30px, -0.5846rem + -1.7647vw, -15px);
    padding: 18px clamp(15px, 0.5846rem + 1.7647vw, 30px);
    font-size: 18px;
    line-height: 24px;
    cursor: pointer;
  }
  &__current.active {
    background-color: var(--clr-bg-green);
  }
  &__arrow {
    flex: 0 0 auto;
    inline-size: 24px;
    block-size: 24px;
    fill: var(--clr-accent-2);
    transform: rotate(90deg);
    transition: transform var(--tr);
  }
  &__arrow.rotate {
    transform: rotate(-90deg);
  }
}

.select.active {
  border-color: var(--clr-accent-2);
}

.sub-select {
  &__current {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-block-end: 10px;
    font-weight: var(--fw-b);
    cursor: pointer;
  }
  &__arrow {
    flex: 0 0 auto;
    inline-size: 24px;
    block-size: 24px;
    fill: var(--clr-accent-2);
    transform: rotate(90deg);
    transition: transform var(--tr);
  }
  &__arrow.rotate {
    transform: rotate(-90deg);
  }
  &__options {
    overflow: hidden;
    display: grid;
    row-gap: 10px;
    max-block-size: 0;
    margin-inline-start: clamp(15px, 0.5846rem + 1.7647vw, 30px);
    transition: max-block-size var(--tr);
  }
  &__options.expand {
    max-block-size: 300px;
  }
  &__option {}
}
