.search-block {
  position: relative;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
  margin-bottom: 30px;
  .menu-filter>.menu-item:nth-child(1) {
    position: relative;
    z-index: 4;
  }
  .menu-filter>.menu-item:nth-child(2) {
    position: relative;
    z-index: 3;
  }
  .menu-filter>.menu-item:nth-child(3) {
    position: relative;
    z-index: 2;
  }
  .menu-filter>.menu-item:nth-child(4) {
    position: relative;
    z-index: 1;
  }
  .input.search {
    flex-grow: 1;
  }
}

@media (max-width: 900px) {
  .search-block {
    .input.search {
      width: 100%;
      max-width: unset;
    }
  }
}
