.page-vacancy {
  margin-block-end: 0;
  .vacancy-section {
    .subtitle {
      margin-block-end: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
    }
    .box-hl {
      margin-block-end: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
       :last-child {
        margin-block-end: 0;
      }
    }
    h3 {
      margin-block-end: 20px;
    }
    h4 {
      margin-block-end: 20px;
    }
    .list {
      margin-block-end: 20px;
      li {
        margin-block-end: 10px;
      }
    }
  }
  .vacancy-form-block {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    column-gap: 30px;
    margin-block: clamp(1.88rem, calc(0.85rem + 5.12vw), 5.00rem);
    .img {
      overflow: hidden;
      grid-column: 1 / span 3;
      grid-row: 1 / span 1;
      inline-size: 301px;
      block-size: 409px;
      border-radius: 40px;
      img {
        block-size: 100%;
        border-radius: 40px;
      }
    }
    .contacts-form-block {
      grid-column: 4 / span 6;
      grid-row: 1 / span 1;
      justify-self: end;
      max-inline-size: 522px;
    }
  }
  .vacancies-other-block {
    padding-block-start: 20px;
    border-block-start: 1px solid var(--clr-border);
    h2 {
      margin-block-end: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
    }
    &>.btn-vacancy+.btn-vacancy {
      margin-block-start: 10px;
    }
  }
}

@media (max-width: 767px) {
  .page-vacancy {
    .vacancy-form-block {
      grid-template-columns: repeat(9, 1fr);
      column-gap: 0;
      .img {
        display: none;
      }
      .contacts-form-block {
        grid-column: 1 / span 9;
        grid-row: 1 / span 1;
        justify-self: unset;
        max-inline-size: unset;
      }
    }
  }
}
