.card-event-block.auto-grid {
  --min-w: 315px;
}

@media (max-width: 710px) {
  .card-event-block {
    overflow-x: scroll;
    display: flex;
    gap: 5px;
    padding-block-end: 20px;
    .card-event {
      min-inline-size: 270px;
    }
    .card-event .card-bottom {
      padding: 15px;
    }
  }
  .card-event-block::-webkit-scrollbar {
    height: .5rem;
  }
  .card-event-block::-webkit-scrollbar-track {
    background-color: var(--clr-bg-green);
    border-radius: 100vw;
  }
  .card-event-block::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent-2);
    border-radius: 100vw;
  }
}
