.our-mission {
  display: grid;
  grid-template-areas: 'imgtop imgtop blocktop' 'blockbtm imgbtm imgbtm';
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px;
  margin-block-end: 40px;
  .block {
    padding: clamp(1.25rem, calc(0.64rem + 3.07vw), 3.13rem);
    border-radius: 30px;
    background: var(--clr-bg-green);
    .desc {
      display: flex;
      flex-direction: column;
      block-size: 100%;
    }
    .subheading {
      margin-block-end: 10px;
      color: var(--clr-light-text);
      font-size: 18px;
    }
    h3 {
      margin-block-end: 15px;
      text-transform: uppercase;
    }
    .text {
      flex-grow: 1;
    }
    .btn-ghost {
      margin-block-start: 18px;
    }
  }
  .img {
    overflow: hidden;
    block-size: 100%;
    border-radius: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
    border-radius: 40px;
    img {
      block-size: 100%;
      border-radius: 40px;
      border-radius: 40px;
    }
  }
  .block--top {
    grid-area: blocktop;
    inline-size: 412px;
  }
  .block--bottom {
    grid-area: blockbtm;
    inline-size: 412px;
  }
  .img--top {
    grid-area: imgtop;
  }
  .img--bottom {
    grid-area: imgbtm;
  }
}

@media (max-width: 991px) {
  .our-mission {
    grid-template-areas: 'top' 'btm';
    grid-template-columns: 1fr;
    grid-template-rows: max-content max-content;
    column-gap: unset;
    row-gap: 30px;
    .block--top {
      grid-area: top;
      inline-size: 412px;
    }
    .block--bottom {
      grid-area: btm;
      inline-size: 412px;
    }
    .img--top {
      grid-area: top;
    }
    .img--bottom {
      grid-area: btm;
    }
    .block--top,
    .block--bottom {
      justify-self: end;
      position: relative;
      z-index: 1;
      inline-size: unset;
      max-inline-size: 417px;
      // margin-block-start: 90px;
      margin-block-start: 140px;
      margin-inline-start: 15px;
    }
    .img--top,
    .img--bottom {
      position: relative;
      z-index: 0;
      inline-size: 270px;
      inline-size: 800px;
      inline-size: clamp(16.88rem, calc(1.08rem + 78.99vw), 50.00rem);
      block-size: 380px;
      block-size: 200px;
      block-size: clamp(12.50rem, calc(7.13rem + 26.83vw), 23.75rem);
    }
  }
}

@media (max-width: 479px) {
  .our-mission {
    margin-inline-start: -15px;
    .img--top,
    .img--bottom {
      border-radius: 0 40px 40px 0;
      img {
        border-radius: 0 40px 40px 0;
      }
    }
  }
}
