.events-list-wrap {
  display: none;
  margin-block-end: clamp(1.88rem, calc(1.26rem + 3.07vw), 3.75rem);
  .subtitle {
    margin-block-end: 20px;
    padding-block-end: 20px;
    border-block-end: 1px solid var(--clr-border);
    span {
      color: var(--clr-accent-2);
    }
  }
  .events-list {
    display: grid;
    row-gap: 20px;
    li {
      display: grid;
      grid-template-columns: max-content 1fr;
      column-gap: 10px;
      row-gap: 3px;
      .e-date {
        grid-column: 1 / span 1;
        grid-row: 1 / span 1;
        display: grid;
        justify-content: center;
        align-items: center;
        block-size: 40px;
        inline-size: 40px;
        color: var(--clr-white);
        background-color: var(--clr-accent);
        border-radius: 100vw;
      }
      .e-day {
        grid-column: 1 / span 1;
        grid-row: 2 / span 1;
        justify-self: center;
        font-size: 14px;
        color: var(--clr-light-text);
      }
      .e-content {
        grid-column: 2 / span 1;
        grid-row: 1 / span 2;
        padding: 10px;
        color: var(--clr-text);
        background-color: var(--clr-bg-green);
        font-size: 14px;
        border: 1px solid var(--clr-border);
        border-radius: 10px;
        display: grid;
        transition: var(--tr);
        .e-name {
          font-weight: var(--fw-b);
        }
        .e-place {
          color: var(--clr-light-text);
          font-size: 12px;
          transition: color var(--tr);
        }
      }
      .e-content:hover,
      .e-content:focus-visible {
        color: var(--clr-white);
        background-color: var(--clr-accent-2);
        .e-place {
          color: var(--clr-white);
        }
      }
    }
  }
}

@media (max-width: 680px) {
  .events-list-wrap {
    display: block;
  }
}
