.menu-top-wrap {
  height: 100%;
}

.menu-top {
  display: flex;
  align-items: center;
  gap: 40px;
  gap: clamp(0.63rem, calc(-6.43rem + 11.03vw), 2.50rem);
  height: 100%;
  .menu-item {
    position: relative;
    display: flex;
    align-items: center;
    height: 100%;
  }
  .has-menu-sub .link-item::after {
    content: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.8335 8.33337L10.0002 12.5L14.1668 8.33337H5.8335Z' fill='black'/%3E%3C/svg%3E%0A");
    inline-size: 20px;
    block-size: 20px;
    transition: transform var(--tr);
  }
  .has-menu-sub .link-item.active::after {
    content: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.8335 8.33337L10.0002 12.5L14.1668 8.33337H5.8335Z' fill='%231FC3A1'/%3E%3C/svg%3E%0A");
    transition: transform var(--tr);
  }
  .has-menu-sub:hover .link-item::after {
    content: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.83325 8.33337L9.99992 12.5L14.1666 8.33337H5.83325Z' fill='%231FC3A1'/%3E%3C/svg%3E");
    transform: rotateZ(-180deg);
  }
  .has-menu-sub:hover .link-item.active::after {
    content: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.8335 8.33337L10.0002 12.5L14.1668 8.33337H5.8335Z' fill='%231FC3A1'/%3E%3C/svg%3E%0A");
    transform: rotateZ(-180deg);
  }
  .menu-item:hover,
  .menu-item:focus-visible {
    .link-item {
      color: var(--clr-accent-2);
    }
    .menu-sub {
      display: grid;
      .link-item {
        color: var(--clr-text);
      }
      .link-item:hover,
      .link-item:focus-visible {
        color: var(--clr-accent-2);
      }
      .link-item::after {
        display: none;
      }
    }
  }
  .link-item {
    display: flex;
    align-items: center;
    height: 100%;
    color: var(--clr-text);
    font-size: 15px;
    line-height: 20px;
    transition: color var(--tr);
  }
  .link-item.active {
    color: var(--clr-accent-2);
  }
  .menu-sub {
    position: absolute;
    top: 80%;
    left: 0;
    display: none;
    gap: 10px;
    width: max-content;
    padding: 30px;
    background-color: var(--clr-bg-green);
    border-radius: 20px;
  }
}

@media (max-width: 1023px) {
  .menu-top-wrap {
    display: none;
    position: relative;
    padding-top: 14px;
  }
  .menu-top {
    align-items: flex-start;
    .menu-item {
      height: unset;
    }
    .menu-item.has-menu-sub:hover,
    .menu-item.has-menu-sub:focus-visible {
      &>.link-item {
        padding-bottom: 50px;
      }
    }
    .menu-sub {
      grid-auto-flow: column;
      top: 60%;
      padding: 0px 10px 10px;
      background-color: transparent;
      border-radius: unset;
    }
    .menu-sub:hover,
    .menu-sub:focus-visible {
      display: block;
    }
  }
}

@media (max-width: 799px) {
  .menu-top-wrap {
    overflow-y: auto;
    overflow-x: hidden;
    align-self: start;
    width: 250px;
    padding-top: 30px;
  }
  .menu-top-wrap::-webkit-scrollbar {
    width: .5rem;
  }
  .menu-top-wrap::-webkit-scrollbar-track {
    background-color: var(--clr-bg-green);
    border-radius: 100vw;
  }
  .menu-top-wrap::-webkit-scrollbar-thumb {
    background-color: var(--clr-accent-2);
    border-radius: 100vw;
  }
  .menu-top {
    display: grid;
    gap: 20px;
    height: max-content;
    margin: 0;
    padding: 0;
    .menu-item {
      flex-direction: column;
      display: grid;
    }
    .menu-item.has-menu-sub:hover,
    .menu-item.has-menu-sub:focus-visible {
      &>.link-item {
        padding-bottom: 0px;
      }
    }
    .menu-item:hover,
    .menu-item:focus-visible {
      margin: 0;
      padding: 0;
      .link-item {
        margin: 0;
        padding: 0;
      }
    }
    .menu-sub {
      position: static;
      top: 0;
      display: grid;
      grid-auto-flow: unset;
      // display: none;
      gap: 15px;
      max-width: 280px;
      margin: 0 0 0 20px;
      padding: 15px 0 0 0;
    }
  }
  .menu-top .has-menu-sub .menu-sub .link-item::after {
    display: none;
  }
}
