.our-products {
  display: grid;
  grid-template-areas: 'desc buds drugs';
  grid-template-columns: 1fr 1fr 1fr;
  gap: clamp(0.63rem, calc(0.22rem + 2.05vw), 1.88rem);
  margin-block-end: clamp(4.38rem, calc(3.97rem + 2.05vw), 5.63rem);
  h2 {
    margin-bottom: 20px;
    text-transform: uppercase;
  }
  .block {
    overflow: hidden;
    position: relative;
    border-radius: 30px;
    border-radius: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
    img {
      height: 100%;
      border-radius: 30px;
      border-radius: clamp(1.25rem, calc(1.05rem + 1.02vw), 1.88rem);
    }
  }
  .desc {
    grid-area: desc;
    align-self: start;
    padding: 50px;
    padding: clamp(1.25rem, calc(0.64rem + 3.07vw), 3.13rem);
    background-color: var(--clr-bg-green);
  }
  .buds {
    grid-area: buds;
    margin-top: 40px;
  }
  .drugs {
    grid-area: drugs;
    margin-top: 40px;
  }
  .buds,
  .drugs {
    max-height: 380px;
  }
  h3 {
    position: absolute;
    bottom: 102px;
    left: 40px;
    color: var(--clr-white);
  }
  .btn {
    position: absolute;
    bottom: 35px;
    left: 40px;
  }
}

@media (max-width: 991px) {
  .our-products {
    grid-template-areas: 'desc desc' 'buds drugs';
    grid-template-columns: 1fr 1fr;
    .buds {
      margin-top: unset;
    }
    .drugs {
      margin-top: unset;
    }
  }
}

@media (max-width: 679px) {
  .our-products {
    grid-template-areas: 'desc' 'buds' 'drugs';
    grid-template-columns: 1fr;
    .buds,
    .drugs {
      aspect-ratio: 1;
      max-width: 412px;
    }
  }
}
