.slider-drug {
  margin-bottom: 10px;
  .img {
    aspect-ratio: 1.2511 / 1;
    max-block-size: 418px;
    padding: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
    border: 1px solid var(--clr-bg-green);
    border-radius: clamp(1.25rem, calc(0.84rem + 2.05vw), 2.50rem);
    img {
      height: 100%;
    }
  }
}

.thumbnails {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  .thumbnail {
    overflow: hidden;
    aspect-ratio: 1;
    max-block-size: 168px;
    padding: 15px;
    border: 1px solid var(--clr-bg-green);
    border-radius: clamp(0.63rem, calc(0.42rem + 1.02vw), 1.25rem);
    cursor: pointer;
    img {
      height: 100%;
      opacity: .3;
    }
  }
  .thumbnail.is-active {
    img {
      opacity: 1;
    }
  }
}
